import { IStrophe } from "./Strophe.Interface";
import { handleStropheLog } from "../Managers/ServiceManager";

var Strophe = require('strophe'), 
    $build  = (name?: string, attrs?: any) => { return new Strophe.Builder(name, attrs); },
    $iq = (attrs?: any) => { return new Strophe.Builder("iq", attrs); },
    $msg = (attrs?: any) => { return new Strophe.Builder("message",  attrs); },
    $pres = (attrs?: any) => { return new Strophe.Builder("presence", attrs); };


// This has to be in a seperate file or mocha will fail when it tries to import strophe due to the window dependency.
export const concreteStrophe: IStrophe = { 
    $iq: $iq,
    $msg: $msg,
    $pres: $pres,

    log: Strophe.log,
    buildConnection: function(service: string, options?: Strophe.ConnectionOptions) { return new Strophe.Connection(service, options); }
};

// Strophe.log is going to be a problem child...
Strophe.log = function(level: number, message: string) { 
    handleStropheLog(level, message);
};


