import { Reducer } from "redux";

import { AlertState, Alert } from "../State/Alert.State";
import { alertStateActionTypes, AlertStateAction } from "../Action/Alert.State.Action";

let initialState: AlertState = {alerts: [], rooms: [], user: ""};
export const alertStateReducer: Reducer<AlertState> = (state: AlertState = initialState, action: AlertStateAction) => {
    let updatedState: AlertState;
    switch(action.type) {
        case alertStateActionTypes._SET_USER:
            if(action.payload.user === null || action.payload.user === null) {
                throw("No user provided to set alert state.");
            }
            updatedState = {
                ...state, 
                user: action.payload.user
            };
            break;
        case alertStateActionTypes.CREATE_ALERT:
            if(action.payload.alert === null || action.payload.alert === undefined) {
                throw("No alerts provided to create.");
            }
            let updatedAlerts = state.alerts;
            updatedAlerts = updatedAlerts.concat(action.payload.alert);
            updatedState = { 
                ...state,
                alerts: updatedAlerts
            };
            break;
        case alertStateActionTypes.ALERT_MESSAGE:
            if(action.payload.alertIndex === null || action.payload.alertIndex === undefined) { 
                throw("No alerts index specified for thread messages update."); 
            }
            if(action.payload.alertMessage === null || action.payload.alertMessage === undefined) {
                throw("No new alerts messages provided.");
            }
            const updatedMessages = state.alerts[action.payload.alertIndex].messages.concat(action.payload.alertMessage);
            let wasFromCurretnUser = (action.payload.alertMessage.getAttribute("from").split("/")[0].toLowerCase() === state.user);
            const updatedAlert: Alert = { 
                ...state.alerts[action.payload.alertIndex], 
                messages: updatedMessages, 
                unreadCount: (wasFromCurretnUser) ? 0 : state.alerts[action.payload.alertIndex].unreadCount + 1
            };
            let alerts = state.alerts.slice(0, action.payload.alertIndex);
            alerts = alerts.concat(updatedAlerts); 
            alerts = alerts.concat(state.alerts.slice(action.payload.alertIndex+1));
            updatedState = {
                ...state,
                alerts: alerts
            };
            break;
        case alertStateActionTypes.ALERT_READ:
            if(action.payload.alertIndex === null || action.payload.alertIndex === undefined) {
                throw("Thread index not provided to specify what thread was read.");
            }
            const readAlert: Alert = { 
                ...state.alerts[action.payload.alertIndex],
                unreadCount: 0
            };
            const markedAlerts = sliceAlerts(state.alerts, readAlert, action.payload.alertIndex);
            updatedState = {
                ...state,
                alerts: markedAlerts
            };
        case alertStateActionTypes.UPDATE_PARTICIPANT_STATE:
            if(action.payload.alertIndex === null || action.payload.alertIndex === undefined){
                throw("No thread provided for the chat state update");
            }
            else if(action.payload.participant === null || action.payload.participant === undefined){
                throw("No participant provided to update chat state for");
            }
            let participantIndex = -1;
            state.alerts[action.payload.alertIndex].participants.forEach((participant, index) => {
                if(participant.jid.toLowerCase() === action.payload.participant.jid.toLowerCase()){ participantIndex = index; }
            });
            if(participantIndex = -1) { throw(`Participant not found in thread: ${action.payload.participant.jid.toLowerCase()}, ${state.alerts[action.payload.alertIndex].id}`)}
            
            break;
        default: 
            updatedState = state;
    }
    return updatedState;
};

/**
 * Slice the passed threads array inserting the passed thread at index, leaving the original array unaltered.
 * @param {Thread[]} threads - The threads to be sliced
 * @param {Thread} updatedThread - The thread to be inserted at index 
 * @param {number} index - The index at which to slice
 */
function sliceAlerts(alerts: Alert[], updatedAlert: Alert, index: number): Alert[] {
    let updatedAlerts = alerts.slice(0, index).concat(updatedAlert).concat(alerts.slice(index+1));
    return updatedAlerts;
}


