import { Participant, participantChatStates } from "../../State/Chat.State";
import { StringManipulationManager } from "../StringManipulation/StringManipulationManager";

/** 
 * Class containing static methods that are used to parse data out of a stanza
*/
export class StanzaHelper {

    /**
    * Get the Jid from a given stanza
    * @param {Element} stanza - The XMPP stanza to have its Jid determined 
    */
    static getJid(stanza: Element): string {
        return stanza.getElementsByTagName("message")[0] ?
            stanza.getElementsByTagName("message")[0].getAttribute("from").split("/")[0].toLowerCase() :
            stanza.getAttribute("from").split("/")[0].toLowerCase();
    }

    /**
     * Get the timestamp of the message
     * @param {Element} message - The stanza whose timestamp is to be extracted
     */
    static GetDelayOrCreatedTimestamp(message: Element): string {
        let delayTimestamp = this.GetDelayTimestamp(message);
        if (delayTimestamp != "") return delayTimestamp;

        let createdTimestamp = this.GetCreatedTimestamp(message);
        if (createdTimestamp != "") return createdTimestamp;

        return "";
    }

    private static GetDelayTimestamp(message: Element): string {
        let timestamp = "";
        let delayElements = message.getElementsByTagName("delay");

        if (delayElements.length == 1) {
            let delayElement = delayElements[0];
            if (delayElement.hasAttribute("stamp")) {
                timestamp = delayElement.getAttribute("stamp");
            }
        }

        return timestamp;
    }

    private static GetCreatedTimestamp(message: Element): string {
        let timestamp = "";
        let headers: HTMLElement[] = Array.prototype.slice.call(message.getElementsByTagName("header"));
        let createdHeaders = headers.filter(function (element) {
            return element.getAttribute("name") == "Created";
        });

        if (createdHeaders.length == 1) {
            timestamp = createdHeaders[0].textContent;
        }

        return timestamp;
    }

    /**
    * Determine the thread id of the passed stanza.
    * @param {Element} stanza - The XMPP stanza to have its id determined 
    */
    public static getThreadId(stanza: Element): string {
        let threadId: string;
        if (stanza.getElementsByTagName("thread")[0]) {
            if (stanza.getElementsByTagName("thread")[0].getAttribute("id")) {
                threadId = stanza.getElementsByTagName("thread")[0].getAttribute("id");
            }
        }
        if (!threadId) {
            threadId = "";
            let addresses = StanzaHelper.getParticipantsFromStanza(stanza);
            addresses.forEach((address) => { threadId += address.jid.toLowerCase(); });
        }
        return threadId;
    }

    public static getLastElementTextContent(stanza: Element) {
        let last = stanza.getElementsByTagName("last")[0];
        if (!last) {
            throw ("The stanza passed did not contain a last element");
        }

        return last.textContent;
    }

    /**
     * Get the participant of the message.
     * Checks "nick" tag first and falls back to the "from" attribute.
     * @param {Element} message - The stanza whose Participant is to be extracted
     */
    public static getParticipantName(stanza: Element): string {
        let participantName;

        if (stanza.getElementsByTagName("nick")[0]) {
            participantName = stanza.getElementsByTagName("nick")[0].textContent;
        } else if (stanza.getElementsByTagName("message")[0]) {
            participantName = stanza.getElementsByTagName("message")[0].getAttribute("from").split("/")[0].toLowerCase();
        } else if (stanza.getAttribute("from")) {
            participantName = stanza.getAttribute("from").split("/")[0].toLowerCase();
        }
        return participantName;
    }

    /**
     * Get the id attribute from the passed stanza.
     * NOTE: If the message is an archived message the id attribute that is retrieved is the id of the INNER <message /> tag
     * @param {Element} stanza - The stanza to get the id attribute from
     * @returns {string} - The message's original id attribute
     */
    public static getMessageId(stanza: Element) {
        let tags = stanza.getElementsByTagName("message");
        if (tags.length === 0) {
            return stanza.getAttribute("id");
        }
        else if (tags.length === 1) {
            return tags[0].getAttribute("id");
        }
        else {
            // I don't pretend to know what the eventual shape of our complex message stanzas will be, but I believe the inner most <message /> will always be the one we care about 
            return tags[tags.length - 1].getAttribute("id");
        }
    }

    /**
    * Sort the passed list of participants. NOTE: passed array is NOT treated as immutable
    * @param {Participant} participants - The participants to sort
    * @returns 
    */
    public static sortParticipants(participants: Participant[]) {
        participants.sort((member1, member2) => {
            if (member1.jid.toLowerCase() > member2.jid.toLowerCase()) { return 1; }
            if (member2.jid.toLowerCase() < member2.jid.toLowerCase()) { return -1; }
            return 0;
        });
        return participants;
    }


    /**
     * Get the participants of a conversation from the passed stanza
     * @param stanza - The stanza from which to extract participants
     * @returns {Participant[]} - A list of participants  
     */
    public static getParticipantsFromStanza(stanza: Element): Participant[] {
        let threadMemberTags = stanza.getElementsByTagName("address");
        let participants: Participant[] = [];
        for (let i = 0; i < threadMemberTags.length; i++) {
            participants.push({
                jid: threadMemberTags[i].getAttribute("jid").toLowerCase(),
                name: threadMemberTags[i].getAttribute("desc"),
                state: participantChatStates.Inactive
            });
        }

        let participantName = StanzaHelper.getParticipantName(stanza);

        let from: Participant = {
            name: participantName,
            jid: StanzaHelper.getJid(stanza),
            state: participantChatStates.Active
        };

        participants.push(from);

        StanzaHelper.sortParticipants(participants);
        return participants;
    }

    /**
     * Get the alert participants of a notification from the passed stanza.
     * @param stanza - The stanza from which to extract participants
     * @returns {Participant[]} - A list of participants  
     */
    public static getAlertParticipantFromStanza(stanza: Element): Participant[] {

        let participants: Participant[] = [];

        // The participant name for the alert/notification, should be a fail safe backup here, implement with the true alert implementation.
        let participantName = stanza.getAttribute("from").split("/")[0].toLowerCase();

        let from: Participant = {
            name: participantName,
            jid: StanzaHelper.getJid(stanza),
            state: participantChatStates.Active
        };

        participants.push(from);

        return participants;
    }

    /**
     * Get the timestamp of the message in the specified format if one is provided
     * @param {Element} message - The stanza whose timestamp is to be extracted
     * @param {string} format - The format for the timestamp
     */
    public static getFormattedTimestamp(message: Element, format?: string): string {
        let timestamp = StanzaHelper.GetDelayOrCreatedTimestamp(message);
        let formattedTimestamp = StringManipulationManager.formatTimestamp(timestamp, format);
        return formattedTimestamp;
    }

    /**
    * Get the body content of the stanza
    * @param {Element} stanza - The stanza whose body content is to be extracted 
    */
    public static getBodyContent(stanza: Element): string { // TODO this will be consumed in the views. Make rename to remove the _
        // NOTE: Assumption that there will only ever be one body element.
        let body = stanza.getElementsByTagName("body");
        if (body[0]) {
            return body[0].textContent;
        }
        else {
            return null;
        }
    }

}


