import { Reducer } from "redux";

import { AudibleSoundsStateAction, audibleSoundsStateActionTypes } from "../Action/AudibleSounds.State.Action";
import { AudibleSoundState } from "../State/Notification-Sounds/AudibleSounds.State";

let initialState: AudibleSoundState = { Sound: null };
export const audibleSoundsStateReducer: Reducer<AudibleSoundState> = (state: AudibleSoundState = initialState, action: AudibleSoundsStateAction) => {
    let updatedState: AudibleSoundState;
    switch(action.type) {
        case audibleSoundsStateActionTypes.SET_SOUND_PRESENCE: 
            if (action.payload.audibleSounds === null || action.payload.audibleSounds === undefined) { throw("Audible sound data cannot be null or undefined."); }
            updatedState = { Sound: action.payload.audibleSounds };
            return updatedState;
        default: 
            return state;
    }
};


