// 3rd party libraries
import * as ReactDOM from "react-dom";
import * as React from "react";

// Managers
import { RouteManager } from "../Routing/RouteManager";
import { IncomingMessageManager } from "../XMPP/IncomingMessageManager";
import { OutgoingMessageManager } from "../XMPP/OutgoingMessageManager";
import { EjabberdConnectionManager } from "../XMPP/EjabberdConnectionManager";
import { SearchManager } from "../Search/SearchManager";
import { MessagesUiManager } from "../UI/MessagesUI.Manager";
import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";
import { ConnectivityManager } from '../NetworkConnectivity/Connectivity.Manager';
import { LogManager } from "../Log/LogManager";
import { FeatureManager } from "../FeatureManagement/FeatureManager";
import { AudibleSoundsManager } from "../AudibleSounds/AudibleSounds.Manager";
import { AppTitleManager } from "../../Components/AppTitle/AppTitle.Manager";

// Renderables
import { MessagesContainer } from "../../UI/Messages/Messages.Container";
import { LoginPresenter } from "../../UI/Login/Login.Presenter";
import { ErrorPresenter } from "../../UI/Generics/Error.Presenter";

//TODO I have no idea how to test this yet. Putting it off until later.
export class RenderingManager {

    /**
     * Render the main messaging part of the application at the element whose id is the passed value.
     * @param {RouteManager} router - The part of the application that controls the users URL bar.
     * @param {EjabberdConnectionManager} messanger - Responsible for management of a connection to the xmpp server
     * @param {IncomingMessageManager} messanger - Responsible for handling the incoming xmpp messages
     * @param {OutgoingMessageManager} messanger - Responsible for the composition of stanzas
     * @param {SearchManager} searcher - Manager that hits the .NET API to find users of the system
     * @param {MessagesUiManager} uiMessagesManager - Manager that controls the ui state of the messaging portion of the application.
     */

    public renderApp(router: RouteManager,
        incomingMessageManager: IncomingMessageManager,
        outgoingMessageManager: OutgoingMessageManager,
        ejabberdConnectionManager: EjabberdConnectionManager,
        searcher: SearchManager,
        uiMessagesManager: MessagesUiManager,
        storeAccess: StoreAccessManager,
        elementId: string,
        logger: LogManager,
        connectivityManager: ConnectivityManager,
        featureManager: FeatureManager,
        audibleSoundsManager: AudibleSoundsManager,
        appTitleManager: AppTitleManager) {

        try {
            ReactDOM.render(
                <MessagesContainer
                    incomingMessageManager={incomingMessageManager}
                    outgoingMessageManager={outgoingMessageManager}
                    ejabberdConnectionManager={ejabberdConnectionManager}
                    peopleSearcher={searcher}
                    uiManager={uiMessagesManager}
                    storeAccess={storeAccess}
                    logger={logger}
                    connectivityManager={connectivityManager}
                    featureManager={featureManager}
                    audibleSoundsManager={audibleSoundsManager}
                    appTitleManager={appTitleManager} />,
                document.getElementById(elementId)
            );
        }
        catch (ex) {
            console.error(ex);
        }
    }

    /**
     * Renders the spinning wheel of patience
     * @param {string} elementId - The id of the element to render on
     */
    public renderLoadingScreen(elementId: string) {
        ReactDOM.render(
            <div>
                <LoginPresenter />
            </div>,
            document.getElementById(elementId),
        );
    }

    /**
     * Renders the error page
     * @param {string} elementId - The id of the element to render on
     * @param {string} message - The optional message to render on the error page.
     */
    public renderErrorPage(elementId: string, message?: string) {
        ReactDOM.render(
            <div>
                <ErrorPresenter message={message} />
            </div>,
            document.getElementById(elementId),
        );
    }
}



