import { createBrowserHistory, History, LocationDescriptorObject } from "history";
import { UserManager, UserManagerSettings, User } from "oidc-client";

import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";

import { AuthenticationStateAction, authenticationStateActionTypes } from "../../Action/Authentication.State.Action";

import { EnvironmentConfiguration } from "../Configuration/ConfigurationManager";

const baseURL = "/client";

/**
 * A class to handle the programatic manipulation of the applications routes and oidc connection.
 */
export class OidcManager {
    
    /**
     * NOTE: This is NOT a custom manager class. This is the name the oidc client chose for its class that manages connections to identity server
     * The oidc manager's connection to identity server.
     */
    public _manager: UserManager; 

    /**
     * The manager's access to the redux store.
     */
    public _storeAccess: StoreAccessManager;

    /**
     * Creates a new instance of the route manager. Defines the oidc user manager and creates the browser history.
     * @param oidcConfig 
     */
    constructor(oidcConfig: OidcConfig, storeAccess: StoreAccessManager) {
        // TODO this absolutely has to be injected. Tests agains this class fail in mocha because of how oidc instantiates the UserManger class.
        this._manager = new UserManager(oidcConfig);
        this._storeAccess = storeAccess;
    }

    /**
     * Initiate the signin process. This will redirect to the identity server login page.
     * @returns {Promise} - The promise that can optionally be resolved by the consumer to handle errors.
     */
    public signin() {
        return this._manager.signinRedirect();
    }
    
    /**
     * Run the signinRedirectCallback (bad name from oidc) method and return the promise it generates so the consumer can do as they wish with it.
     * @returns {Promise} - The promise to be resolved after the authentication to identity server is complete.
     */
    public signinEnd() {
        return this._manager.signinRedirectCallback();
    }

    /**
     * Run the getUser() method and return the promise returned from oidc for the consumer to evaluate.
     * @returns {Promise} - A promise that, when resolved, exposes the currently authenticated users details.
     */
    public getUser() {
        return this._manager.getUser();
    }

    /**
     * Load the currently authenticated user's details into the redux store
     * @param {User} user - The user whose information is to be loaded
     */
    public loadAuthData(user: User): void {
        let action: AuthenticationStateAction = { type: authenticationStateActionTypes.NEW_AUTH, payload: {
            id_token: user.id_token,
            access_token: user.access_token,
            token_type: user.token_type,
            scope: user.scope,
            state: user.state,
            session_state: user.session_state,
            expires_at: user.expires_at,
            profile: user.profile
        }};
        this._storeAccess.dispatchAction(action);
    }
}


export interface OidcConfig { 
    authority: string,
    client_id: string,
    redirect_uri: string,
    response_type: string,
    scope: string,
    post_logout_redirect_uri: string
}


