import * as React from "react";
import { addFeatureFlags } from "react-redux-feature-flags";
import { Icon } from "../../../Components/Icons/Icon";
import { MainPresenter, SidebarTab } from "../../../State/Messages.UI.State";
import { ThreadsSidebar } from "./Sidebar/Threads.Sidebar.Presenter";
import { MessagesUiManager } from "../../../Managers/UI/MessagesUI.Manager";
import { Thread } from "../../../State/Chat.State";
import { Alert } from "../../../State/Alert.State";
import { FeatureFlag } from "../../../State/FeatureFlags/FeatureFlags.State";

interface SidebarProps {
    user: { name: string, jid: string };
    activeTab: SidebarTab;
    changeActiveTab: (sidebar: SidebarTab) => void;
    changeMainPresenter: (main: MainPresenter) => void;
    changeActiveThread: (activeThreadId: Thread) => void;
    changeActiveAlert: (activeAlertId: Alert) => void;
    uiManager: MessagesUiManager;
    activeThread: Thread;
    threads: Thread[];
    activeAlert: Alert;
    alerts: Alert[];
    featureFlags: FeatureFlag;
}

export const MessagesSideBar: React.StatelessComponent<SidebarProps> = (props: SidebarProps) => {
    return (

        <aside>

            <div className="colHeader" data-qa="inbox_title_lbl">
                <span>Messages</span>&nbsp;
                <Icon
                    dataQa="compose_btn"
                    identifier="compose"
                    onClick={() => {
                        props.changeMainPresenter(MainPresenter.compose);
                    }}
                    styles="icon-compose-open"
                />
            </div>
            <ThreadsSidebar
                uiManager={props.uiManager}
                user={props.user}
                changeActiveThread={props.changeActiveThread}
                activeThread={props.activeThread}
                threads={props.threads} />
        </aside>
    );
};



