import axios, {AxiosResponse, AxiosPromise } from "axios";

import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";
import { ProfileState } from "../../State/Profile.State";
import { ProfileStateAction, profileStateActionTypes } from "../../Action/Profile.State.Action";

// I am putting off writing its spec until then.
export interface ProfileConfiguration { 
    root: string,
    users: string
}

interface ProfileApiUsersEndpointResponse extends AxiosResponse { 
    data: ProfileState
}

/**
 * Manager class to handle all pieces related to the users profile
 */
export class ProfileManager { 
    private config: ProfileConfiguration;
    private storeAccess: StoreAccessManager;

    constructor(config: ProfileConfiguration, storeAccess: StoreAccessManager) { 
        this.config = config;
        this.storeAccess = storeAccess;
    }

    /**
     * Get the profile information associated with the passed sub
     * @param {string} userSub - The user's sub 
     */
    public getProfile(userSub: string): AxiosPromise { 
        let url = `${this.config.users}${userSub}` ;
        let token = this.storeAccess.getAccessToken();
        return axios.get(url, { headers: { Authorization: `Bearer: ${token}`} });
    }

    /**
     * Resolve a promise from the getProfile method and store the results in the redux store.
     * @param {ProfileApiUsersEndpointResponse} - The axios promise to resolve
     */
    public resolveGetProfile(response: ProfileApiUsersEndpointResponse) { 
        let action: ProfileStateAction = { 
            type: profileStateActionTypes.SET_PROFILE,
            payload: response.data
        };
        this.storeAccess.dispatchAction(action);
    }
}



