import * as React from "react";

import { MainPresenter } from "../../../State/Messages.UI.State";
import { PersonSearchResult } from "../../../State/PeopleSearch.State";
import { Alert } from "../../../State/Alert.State";
import { Participant, Thread } from "../../../State/Chat.State";

import { EmptyMain } from "./Main/Empty.Main.Presenter";
import { MessageCompose } from "./Main/Compose/MessageCompose.Main.Presenter";
import { ThreadPresenter } from "./Main/Thread/Thread.Main.Presenter";
import { AlertPresenter } from "./Main/Alert/Alert.Main.Presenter";
import { MessagesUiManager } from "../../../Managers/UI/MessagesUI.Manager";


interface MessagesMainProps {

    presenter: MainPresenter;
    changeMainPresenter: (main: MainPresenter) => void;

    user: { name: string, jid: string };
    search: (searchTerm: string) => void;
    people: PersonSearchResult[];
    recipients: PersonSearchResult[];
    updateRecipients: (recipients: PersonSearchResult[]) => void;

    clearMessageContents: () => void;
    updateMessageText: (messageText: string) => void;
    sendMessage: (to: Participant[], message: string) => void;
    thread: Thread;
    alert: Alert;
    uiManager: MessagesUiManager;
}

export const MessagesMain: React.StatelessComponent<MessagesMainProps> = (props: MessagesMainProps) => {

    let componentToShow = {};
    componentToShow[MainPresenter.empty] = <EmptyMain
        changeMainPresenter={props.changeMainPresenter}
    />;

    componentToShow[MainPresenter.compose] = <MessageCompose
        recipients={props.recipients}
        updateRecipients={props.updateRecipients}
        search={props.search}
        people={props.people}
        clearMessageContents={props.clearMessageContents}
        updateMessageText={props.updateMessageText}
        sendMessage={props.sendMessage}
        changeMainPresenter={props.changeMainPresenter}
    />;

    componentToShow[MainPresenter.thread] = <ThreadPresenter
        uiManager={props.uiManager}
        close={props.changeMainPresenter}
        thread={props.thread}
        sendMessage={props.sendMessage}
        user={props.user}
    />;

    componentToShow[MainPresenter.alert] = <AlertPresenter
        close={props.changeMainPresenter}
        alert={props.alert}
        user={props.user}
    />;


    return (componentToShow[props.presenter]);
};






