import { Reducer } from "redux";

import { PeopleSearchState } from "../State/PeopleSearch.State";
import { PeopleSearchAction, peopleSearchActionTypes } from "../Action/PeopleSearch.State.Action";

let initialState: PeopleSearchState = { results: {data: [] } };
export const peopleSearchStateReducer: Reducer<PeopleSearchState> = (state: PeopleSearchState = initialState, action: PeopleSearchAction) => { 
    let updatedState: PeopleSearchState;
    switch(action.type) {
        case peopleSearchActionTypes.SEARCH:
            if(action.payload.data === null || action.payload.data === undefined) { throw("Person search result data must not be null or undefined"); }
            updatedState = { results: { data: action.payload.data }};
            return updatedState;
        case peopleSearchActionTypes.CLEAR:
            updatedState = initialState;
            return updatedState;
        default: 
            return state;
    }
};


