export interface IStrophe { 
    // Stanza builders
    $iq: (attrs?: any) => Builder,
    $msg: (attrs?: any) => Builder,
    $pres: (attrs?: any) => Builder,

    log: (level: number, message: string) => void,
    buildConnection: (service: string, options?: ConnectionOptions) => Connection,
}
// I have no effing idea how Typescript is figuring out what Strophe means here...
export interface Builder extends Strophe.Builder {
}
export interface Connection extends Strophe.Connection {
}
export interface ConnectionOptions extends Strophe.ConnectionOptions {
}
export const connStatus = {ERROR: 0, CONNECTING: 1, CONNFAIL: 2, AUTHENTICATING: 3, AUTHFAIL: 4, CONNECTED: 5, DISCONNECTED: 6, DISCONNECTING: 7, ATTACHED: 8};
export const logLevel = {DEBUG: 0, INFO: 1, WARN: 2, ERROR: 3, FATAL: 4};


