import axios, { AxiosPromise, AxiosRequestConfig, AxiosResponse } from "axios";

import { IHttpRequester } from "../../Injectables/Axios.Injectable";
import { StoreAccessManager } from "../../Managers/StoreAccess/StoreAccessManager";

import { ConfigurationStateAction, configurationStateActionTypes } from "../../Action/Configuration.State.Action";

export class ConfigurationManager {
    /**
     * The environment configuration loaded from env.js
     */
    public envConfig: EnvironmentConfiguration;

    /**
     * The managers access to the redux store
     */
    public _storeAcesss: StoreAccessManager;

    /**
     * The managers tool to make HTTP requests
     */
    public _httpRequester: IHttpRequester;

    /**
     * 
     * @param {StoreAccessManager} storeAccess - The managers access to the redux store
     * @param {EnvironmentConfiguration} envConfig - Environment information to be loaded from env.js outside the bundle
     * @param {IHttpRequester} httpRequester - The manager's tool for making HTTP requests
     */
    constructor(storeAccess: StoreAccessManager, envConfig: EnvironmentConfiguration, httpRequester: IHttpRequester) {
        this.envConfig = envConfig;
        this._storeAcesss = storeAccess;
        this._httpRequester = httpRequester;
    }

    /**
     * Return a promise that will resolve the configuration items.
     * @param {string} url - The endpoint from which to request the configuration items. 
     */
    public getMessagingApiConfigurationItems(): AxiosPromise {
        let axiosConfig: AxiosRequestConfig = { headers: { "Accept": "application/json" } };
        return this._httpRequester.get(this.envConfig.messagingApi.configEndpoint, axiosConfig);
    }

    public getFeatureFlags(): AxiosPromise {
        let token = this._storeAcesss.getAccessToken();
        return axios.get(this.envConfig.messagingApi.featureFlags, { headers: { "Authorization": `Bearer ${token}` } });
    }

    /**
     * Store the items returned from the endpoint in the applications store.
     * @param {ConfigItem[]} configItems - The list of configuration items to be stored in the redux store including the xmpp and identity server information.
     */
    public storeConfigurationItems(configItems: ConfigurationItem[]): void {
        let action: ConfigurationStateAction = {
            type: configurationStateActionTypes.UPDATE_ALL,
            payload: {
                xmppUri: null,
                xmppPort: null,
                identityServerUri: null
            }
        };
        configItems.forEach((configItem) => {
            if (configItem.name === "EjabberdServerUri") { action.payload.xmppUri = configItem; }
            else if (configItem.name === "IdentityServerUri") { action.payload.identityServerUri = configItem; }
            else if (configItem.name === "EjabberdServerPort") { action.payload.xmppPort = configItem; }
            else { console.warn("Unknown configuration item"); }
        });
        this._storeAcesss.dispatchAction(action);
    }
}

/**
 * An interface representing a single configuration item.
 */
export interface ConfigurationItem {
    name: string, value: string, created: string, updated: string
}

/**
 * The shape of the data returned from the messaging API endpoint.
 */
export interface ConfigurationEndPointResponse extends AxiosResponse {
    data: {
        groupName: string,
        childConfigurationGroups: {}[],
        configurationItems: ConfigurationItem[],
        created: string,
        updated: string
    }
}

/**
 * Interface representing all configuration information not sourced from an API endpoint.
 */
export interface EnvironmentConfiguration {
    env: string,
    oidc: {
        authority: string,
        client_id: string,
        redirect_uri: string,
        response_type: string,
        scope: string,
        post_logout_redirect_uri: string
    },
    messagingWebClient: {
        enableConsoleLogging: boolean
    },
    messagingApi: {
        root: string,
        search: string,
        configEndpoint: string,
        featureFlags: string
    },
    appUrl: {
        rootUrl: string
    },
    profileApi: {
        root: string,
        users: string
    }
}


