import * as React from "react";

interface ErrorPresenterProps {
    message?: string
}


export const ErrorPresenter: React.StatelessComponent<ErrorPresenterProps> = (props: ErrorPresenterProps) => {
    return (
        <div>
            <h1>An unhandled error has occured. Apologies for the inconvenience.</h1>
            {
                props.message &&
                <h2>This was the error message: {props.message}</h2>
            }
        </div>
    )
};


