import * as React from "react";
import { services } from "../../../../../Managers/ServiceManager";

interface ThreadMessageProps {
    timestamp: string
    fromName: string
    fromTag: string
    isMyMessage: boolean
    key: string
    messageTextContent: string,
    threadMessageRef: any
}

export class ThreadMessage extends React.Component<ThreadMessageProps, {}>  {

    constructor() {
        super();
    }

    render() {

        let authorName = "Me";
        let initials = "";
        const threadClass = this.props.isMyMessage ? "display myMessage" : "display";

        if (!this.props.isMyMessage) {
            authorName = this.props.fromName;
            let names = services.stringManager.resolveNameFromString(authorName);
            initials = services.stringManager.getInitials(names);
        }

        const avatarDiv = this.props.isMyMessage ? <div className="userAvatar"></div> :
            <div className="userAvatar"><span>{initials}</span></div>;

        return (
            <section className={threadClass} data-qa={`${this.props.fromTag}_message_lbl`} ref={this.props.threadMessageRef} >
                <div className="chatBlock">
                    {avatarDiv}
                    <h4 data-qa={`${this.props.fromTag}_name_lbl`}>{authorName}</h4>
                    <h5 data-qa={`${this.props.fromTag}_message_timestamp_lbl`}>{this.props.timestamp}</h5>
                    <p data-qa={`${this.props.fromTag}_message_contents_lbl`}>{this.props.messageTextContent}</p>
                </div>
            </section>
        );
    }

};



