import * as _ from "lodash";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { Icon } from "../../../../../Components/Icons/Icon";
import { MessagesUiManager } from "../../../../../Managers/UI/MessagesUI.Manager";
import { StanzaHelper } from "../../../../../Managers/XMPP/StanzaHelper";
import { Participant, Thread } from "../../../../../State/Chat.State";
import { MainPresenter } from "../../../../../State/Messages.UI.State";
import { SendMessage } from "../../Shared/SendMessage.Presenter";
import { ThreadMessages } from "./Thread.Messages.Presenter";
import { ThreadRecipients } from "./Thread.Recipients.Presenter";

interface ThreadPresenterProps {
    uiManager: MessagesUiManager;
    user: { name: string, jid: string };
    close: (presenter: MainPresenter) => void;
    thread: Thread;
    sendMessage: (to: Participant[], message: string) => void;
}

interface ThreadPresenterState {
    messagesThreadVisible: boolean;
    pendingMessage: boolean;
}

const initialState = {
    messagesThreadVisible: true,
    pendingMessage: false,
};

// This needs to be a container of its own.
export class ThreadPresenter extends React.Component<ThreadPresenterProps, ThreadPresenterState> {
    threadMessages: Element[];

    constructor(props) {
        super(props);
        this.state = initialState;
        this.threadMessages = [];
    }

    render() {
        const recipients = this.parseRecipients();
        const threadHeaderText = this.props.uiManager.buildThreadTitle(this.props.thread, this.props.user.jid);
        return (
            <div id="main" className="isMessageThread">
                <div className="colHeader">
                    <span data-qa="thread_title_lbl">
                        {threadHeaderText}
                    </span>
                    {
                        !this.state.messagesThreadVisible
                            ? <Icon
                                dataQa="thread-messages-back-icon"
                                identifier="arrowLeft"
                                onClick={() => {
                                    this.toggleRecipients(this.props);
                                }}
                                styles="icon-arrow-left"
                            />
                            : null
                    }

                    {
                        this.state.messagesThreadVisible
                            ? <Icon
                                dataQa="thread-participant-icon"
                                identifier="information"
                                onClick={() => {
                                    this.toggleRecipients(this.props);
                                }}
                                styles="icon-information"
                            />
                            : null
                    }

                </div>

                {
                    this.state.messagesThreadVisible
                        ? <div id="messageWrapper" ref="messageList" data-qa="thread-conversation-wrapper">
                            <ThreadMessages thread={this.props.thread}
                                userJid={this.props.user.jid}
                                threadMessagesRef={(element) => this.threadMessages = element}
                            />
                        </div>
                        : <ThreadRecipients participants={this.props.thread.participants}
                            userJid={this.props.user.jid} />
                }

                {
                    this.state.messagesThreadVisible
                        ? <SendMessage
                            placeHolderText="Type your message..."
                            recipients={recipients}
                            sendMessage={this.props.sendMessage}
                            minRows={1}
                            maxRows={6}
                        />
                        : null
                }

            </div>
        );
    }

    /**
     * Navigate to the most recent thread in the main presenter window.
     */
    private navigateToLastMessage() {
        const messageContainer = ReactDOM.findDOMNode(this.refs.messageList);
        if (messageContainer) {
            messageContainer.scrollTop = messageContainer.scrollHeight;
        }
    }

    /**
     * Scroll to newest message if the second newest message was in view when the newest message is added.
     */
    private updateScrollPosition(prevProps: ThreadPresenterProps, prevState: ThreadPresenterState): void {

        const messageContainer = ReactDOM.findDOMNode(this.refs.messageList);
        const currentMessages = _.filter(this.threadMessages, (message) => !!message);
        if (currentMessages.length <= 1 || !messageContainer) {
            return;
        }
        const newestMessage = currentMessages[currentMessages.length - 1];
        const secondNewestMessage = currentMessages[currentMessages.length - 2];

        const oldHeight = messageContainer.scrollHeight - newestMessage.clientHeight;
        const currentPosition = messageContainer.scrollTop + messageContainer.clientHeight;
        const startOfSecondNewest = oldHeight - secondNewestMessage.clientHeight;

        if (this.isLastMessageMine() || (startOfSecondNewest <= currentPosition && currentPosition <= oldHeight)) {
            messageContainer.scrollTop = messageContainer.scrollHeight;
        }
    }

    /**
     * Check to see if the last message in the thread is the current users.
     */
    private isLastMessageMine(): boolean {
        if (!this.props.thread.messages.length) {
            return false;
        }
        const message = this.props.thread.messages[this.props.thread.messages.length - 1];

        const from = StanzaHelper.getJid(message);
        return from.toLowerCase() === this.props.user.jid.toLowerCase();
    }

    /**
     * Hooking into the application lifecycle to allow for the most recent thread to be read first. (Executed after all renders.)
     */
    public componentDidUpdate(prevProps: ThreadPresenterProps, prevState: ThreadPresenterState) {

        //Changing threads should always scroll to bottom.
        if (this.props.thread.id !== prevProps.thread.id) {
            this.navigateToLastMessage();
            return;
        }
        //New message
        if (this.props.thread.messages.length !== prevProps.thread.messages.length) {
            this.updateScrollPosition(prevProps, prevState);
        }
    }

    /**
     * Hooking into the application lifecycle to allow for the most recent thread to be read first. (Executed after initial render.)
     */
    public componentDidMount() {
        this.navigateToLastMessage();
    }

    /**
     * Toggles display of thread recipients. Currently recipients are displayed within the message container,
     * so the messages list thread is also toggled accordingly (which is slated to change with re-skin).
     */
    private toggleRecipients(props) {
        this.setState({ messagesThreadVisible: !this.state.messagesThreadVisible });
        this.setState({ pendingMessage: !this.state.pendingMessage });
    }

    /**
     * Parse message recipients from the participants of the thread (excluding the current user)
     */
    private parseRecipients() {
        const participants = this.props.thread.participants;
        let recipients: Participant[];
        const lowerCasedUserJid = this.props.user.jid.toLowerCase();
        for (let i = 0; i < participants.length; i++) {
            if (participants[i].jid.toLowerCase() === lowerCasedUserJid) {
                recipients = participants.slice(0, i).concat(participants.slice(i + 1, participants.length));
            }
        }
        return recipients;
    }
}



