import { Reducer } from "redux";

import { AppTitleAction, AppTitleActionTypes } from "./AppTitle.Action";
import { AppTitleState } from "./AppTitle.State";
import { DEFAULT_APP_TITLE } from "../../Config/App.Constants";

let initialState: AppTitleState = { title: DEFAULT_APP_TITLE };
export const AppTitleReducer: Reducer<AppTitleState> = (state: AppTitleState = initialState, action: AppTitleAction) => {
    let updatedState: AppTitleState;
    switch (action.type) {
        case AppTitleActionTypes.UPDATE_TITLE:
            if (action.payload.title === null || action.payload.title === undefined) { throw ("App Title data cannot be null or undefined."); }
            updatedState = { title: action.payload.title };
            return updatedState;
        default:
            return state;
    }
};


