/*----------  React  ----------*/
import * as React from "react";

/*----------  Presenters  ----------*/
import { EjabberdConnectivityMessenger } from "../../../../Components/Ejabberd-Connectivity/EjabberdConnectivity.Presenter";
import { AudibleSounds } from "../../../../Components/Audible-Sounds/AudibleSounds.presenter";

/*----------  Managers  ----------*/
import { StoreAccessManager } from "../../../../../Managers/StoreAccess/StoreAccessManager";
import { AudibleSoundsManager } from "../../../../../Managers/AudibleSounds/AudibleSounds.Manager";

/*----------  Constants  ----------*/
import { EjabberdNetworkDrop_Config } from "../../../../../Config/App.Constants";



interface EjabberdConnectivityContainerProps {
    // Redux store manager
    storeAccess: StoreAccessManager,
    audibleSoundsManager: AudibleSoundsManager,

    // Properties for sweetalert modal
    show: boolean,
    title?: string,
    text?: string,
    type?: string,
    showCloseButton?: boolean,
    showCancelButton?: boolean,
    showConfirmButton?: boolean,

    // Properties for soundmanager2
    url: string,
    playStatus?: string
}

interface EjabberdConnectivityContainerState {
    show: boolean
}

export class EjabberdConnectivityMainMessenger extends React.Component<EjabberdConnectivityContainerProps, EjabberdConnectivityContainerState> {
    constructor(props) {
        super(props);
    }

    /**
     * Render Ejabberd messenger along with the audible sounds.
     */
    public render() {
        /**
         * Return the two child components.
         */
        return (
            <div className={"ejabberd-rendering-container"}>
                <EjabberdConnectivityMessenger
                    show={this.props.show}
                    title={EjabberdNetworkDrop_Config.title}
                    text={EjabberdNetworkDrop_Config.text}
                    type={EjabberdNetworkDrop_Config.type}
                    showCloseButton={EjabberdNetworkDrop_Config.showCloseButton}
                    showCancelButton={EjabberdNetworkDrop_Config.showCancelButton}
                    showConfirmButton={EjabberdNetworkDrop_Config.showConfirmButton}
                />
                <AudibleSounds
                    url={this.props.url}
                    playStatus={this.getPlayStatus()}
                    audibleSoundsManager={this.props.audibleSoundsManager}
                />
            </div>
        )
    }

    /**
     * Return the audible sound playStatus from redux.
     * @returns {ejabberdConnection} - The ejabberd state from the redux store.
     */
    private getPlayStatus() {
        return this.props.audibleSoundsManager.getSoundState().Sound.PlayStatus;
    }
}


