import * as React from "react";

import { MainPresenter } from "../../../../../State/Messages.UI.State";
import { PersonSearchResult } from "../../../../../State/PeopleSearch.State";
import { UserSearch } from "../../Shared/UserSearch.Presenter";
import { SendMessage } from "../../Shared/SendMessage.Presenter";
import { Participant } from "../../../../../State/Chat.State";
import { Icon } from "../../../../../Components/Icons/Icon";

interface MessageComposeProps {
    recipients: PersonSearchResult[],
    updateRecipients: (recipients: PersonSearchResult[]) => void,

    search: (searchTerm: string) => void,
    people: PersonSearchResult[],

    sendMessage: (to: { name: string, jid: string }[], message?: string) => void,
    clearMessageContents: () => void,
    updateMessageText: (messageText: string) => void,

    changeMainPresenter: (main: MainPresenter) => void
}

// TODO next tech debt resolution session should be dedicated to stripping this presenters stuff out of the state container and getting it into
// a local state.
export const MessageCompose: React.StatelessComponent<MessageComposeProps> = (props: MessageComposeProps) => {
    let recipientList: Participant[] = [];

    props.recipients.forEach((recipient) => {
        recipientList.push({ name: `${recipient.firstName} ${recipient.lastName}`, jid: recipient.jid.toLowerCase() });
    });

    return (
        <div id="main" className="isComposeNewMessage">
            <div className="colHeader">
                <span data-qa="compose_title_lbl">
                    Compose Message
                </span>
            </div>
            <div className="search-recipient-container">
                <UserSearch people={props.people} search={props.search}
                    recipients={props.recipients} updateRecipients={props.updateRecipients} />
            </div>
            <SendMessage
                placeHolderText="Type your message..."
                recipients={recipientList}
                sendMessage={props.sendMessage}
                minRows={1}
                maxRows={6}
            />
        </div>
    );
};



