import * as React from "react";
import { Thread } from "../../../../State/Chat.State";
import { Icon } from "../../../../Components/Icons/Icon";

import { services } from "../../../../Managers/ServiceManager";
import { MessagesUiManager } from "../../../../Managers/UI/MessagesUI.Manager";
import { StanzaHelper } from "../../../../Managers/XMPP/StanzaHelper";

interface ThreadsSidebarProps {
    uiManager: MessagesUiManager,
    user: { name: string, jid: string },
    threads: Thread[],
    activeThread: Thread, // Note: nullable
    changeActiveThread: (activeThreadId: Thread) => void
}

export const ThreadsSidebar: React.StatelessComponent<ThreadsSidebarProps> = (props: ThreadsSidebarProps) => {
    let threadHeaders: JSX.Element[] = [];
    props.threads.forEach((thread) => {
        const threadHeaderText = props.uiManager.buildThreadTitle(thread, props.user.jid);
        const lastMessageElement = thread.messages[thread.messages.length - 1];
        const lastMessageBody = StanzaHelper.getBodyContent(lastMessageElement);
        const lastMessageTimestamp = StanzaHelper.getFormattedTimestamp(lastMessageElement);
        const avatar = thread.participants.length > 0 ? thread.participants[0].jid : 'baboon';
        const hasNewContentClass = thread.unreadCount > 0 ? 'hasNewContent' : '';
        const activeClass = thread === props.activeThread ? 'thread-picker-active' : '';
        const finalClass = `${hasNewContentClass} ${activeClass}`;

        // FML if there is more than one participant, should probably not display
        // the first participant's initials. No requirements on this ¯\_(ツ)_/¯
        const authorName = thread.participants[0].name;
        const names = services.stringManager.resolveNameFromString(authorName);
        const initials = services.stringManager.getInitials(names);

        threadHeaders.push(
            <li className={finalClass} onClick={e => { props.changeActiveThread(thread); }}
                key={thread.id}>
                <div className={"userAvatar"}><span>{initials}</span></div>
                <h3 data-qa={"thread_addresses_title_lbl"}>{threadHeaderText}</h3>
                <h5 data-qa={"thread_message_timestamp_lbl"}>{lastMessageTimestamp}</h5>
                <p data-qa={"thread_summary_lbl"}>{lastMessageBody}</p>
                <Icon
                    data-qa={"thread_arrow"}
                    identifier="threadArrow"
                    styles="icon-thread-arrow"
                />
            </li>
        )
    });

    return (
        <ul id="inbox" data-qa="thread-inbox">
            {threadHeaders}
        </ul>
    )
};



