import { Store } from "redux";

import { MessagesContainerState } from "../../UI/Messages/Messages.Container";

import { AbstractAction } from "../../Action/_Abstract.Action";
import { AppState } from "../../State/App.State";

import * as _ from 'lodash';

export class StoreAccessManager { 
    public store: Store<AppState>;

    constructor(store: Store<AppState>) {
        this.store = store;
    }

    /*************************************************** SETTERS  *******************************************************/
    /**
     * Dispatches the passed action to the application's redux store. Any child of abstract action will be accepted, but remember to be 
     * specific (i.e. when you instantiate the variable give it a more specific action type) when building the object literals in the 
     * consumer that calls this method.
     * @param {AbstractAction} action - The action to be dispatched
     * @returns {boolean} - True if successful 
     */
    public dispatchAction(action: AbstractAction) { 
        if(!action) { 
            throw("Cannot dispatch a null or undefined action");
        }
        this.store.dispatch(action);
        return true;
    }

    /**************************************************** GETTERS *********************************************************/

    /**
     * Get a document containing the necessary pieces of information to authentication to the XMPP server.
     * @returns {object} - An object with a jid, uri and access token property used to authentication to the XMPP server.
     */
    public getXMPPCredentials(): {jid: string, uri: string, accessToken: string, name: string} { 
        let state = this.store.getState();
        let uri = `wss://${state.configurationState.xmppUri.value}:5280/websocket`;
        let accessToken = state.authenticationState.access_token;
        let jid = this.store.getState().profileState.jabberId;
        let name = this.store.getState().authenticationState.profile.name;
        return {uri: uri, accessToken: accessToken, jid: jid, name: name };
    }

    /**
     * Return a document containing the information pertinent to the messages container's subscription.
     * @returns {object} - A document containing the current sidebar tab, main presenter, recipients and person search results.
     */
     public getMessagesContainerSubData(): MessagesContainerState {
         let state = this.store.getState();

         let subData: MessagesContainerState =  {
             mainPresenter: state.messagesUIState.mainPresenter, 
             activeSidebarTab: state.messagesUIState.sidebarTab, 
             currentRecipients: state.messagesUIState.recipients,
             peopleSearchResults: state.peopleSearchState.results.data,
             serverConnected: state.connectivityState.ejabberdConnection,
             threads: state.chatState.threads,
             activeThread: state.messagesUIState.activeThread,
             alerts: state.alertState.alerts, 
             activeAlert: state.messagesUIState.activeAlert,
             featureFlag: state.featureFlagsState.featureFlags,
             appTitle: state.appTitleState.title
        };
        return subData;
     }

     public getEjabberdStatus() {
         return this.store.getState().connectivityState;
     }

    /**
     * Returns the uri of the identity server used by the client
     */
    public getIdentityServerLocation(): string {
        let state = this.store.getState();
        return state.configurationState.identityServerUri.value;
    }

        /**
     * Checks if the given alert id exists in the application's alert state
     * @param {string} id - The alert id to check for
     * @return {number} - The index at which the id was found. -1 if not found.
     */
    public checkForAlertId(id: string): number { 
        let alertState = this.store.getState().alertState;
        for(let i = 0; i < alertState.alerts.length; i++) {
            if(id === alertState.alerts[i].id) { return i; }
        }
        return -1;
    }

    /**
     * Get the active thread from the UI
     */
    public getActiveAlert() {
        return this.store.getState().messagesUIState.activeAlert;
    }

    /**
     * Get a thread by its index
     * @param {number} i - The index of the thread to get
     */
    public getAlertByIndex(i: number) {
        return this.store.getState().alertState.alerts[i];
    }

    /**
     * Checks if the given thread id exists in the application's chat state
     * @param {string} id - The thread id to check for
     * @return {number} - The index at which the id was found. -1 if not found.
     */
    public checkForThreadId(id: string): number { 
        let chatState = this.store.getState().chatState;
        for(let i = 0; i < chatState.threads.length; i++) {
            if(id === chatState.threads[i].id) { return i; }
        }
        return -1;
    }

    /**
     * Get the active thread from the UI
     */
    public getActiveThread() {
        return this.store.getState().messagesUIState.activeThread;
    }

    /**
     * Get Ejabberd connectivity status
     */
    public getActiveConnection() {
        return this.store.getState().connectivityState.ejabberdConnection;
    }

    /**
     * Get feature flags per user.
     */
    public getFeatFlags() {
        return this.store.getState().featureFlagsState.featureFlags;
    }

    /**
     * Get audible sounds for notifications.
     */
    public getAudibleSounds() {
        return this.store.getState().audibleSoundsState;
    }

    /**
     * Get a thread by its index
     * @param {number} i - The index of the thread to get
     */
    public getThreadByIndex(i: number) {
        return this.store.getState().chatState.threads[i];
    }

    /**
     * Return the user's access token
     */
    public getAccessToken(): string { 
        return this.store.getState().authenticationState.access_token;
    }

    /**
     * Return the current message text from the redux store
     */
    public getMessageText(): string { 
        return this.store.getState().messagesUIState.messageText;
    }

    /**
     * Returns the currently authenticated user's jid.
     * @param {boolean} full - Should the full jid be returned or only the bare
     * @returns {string} - The currently authenticated users jid
     */
    public getCurrentUser() { 
        let user = { 
            jid: this.store.getState().profileState.jabberId,
            name: this.store.getState().authenticationState.profile.name
        };
        return user;
    }

    /**
     * Method to establish subscriptions to the application store.
     * @param {Function} listener - Listener to be invoked on each dispatch. 
     * @returns {Unsubscribe} - The callback to be invoked when the consumer desires to unsubscribe from the store's changes
     */
    public subscribe(listener: () => void) { 
        return this.store.subscribe(listener);
    }

    /**
     * Get the AppTitle State
     */
    public getAppTitleState() {
        const state = this.store.getState();
        return {
            title: state.appTitleState.title
        };
    }

    /**
     * Get threads that have unread messages and are not the active thread.
     */
    //FML This should be replaced with a mechanism to see if a message stanzas within a thread have been read or not.
    public getUnreadThreadsExcludingActive() {
        let activeThreadId = this.getActiveThread() ? this.getActiveThread().id : null;
        let chatState = this.store.getState().chatState;
        return _.filter(chatState.threads, thread => !!thread.unreadCount && thread.id !== activeThreadId);
    }
}


