let uuidv4: () => string = require("uuid/v4");

import { Builder, IStrophe, Connection, Builder as Builder1 } from "../../Injectables/Strophe.Interface";
import { Participant } from "../../State/Chat.State";
import { ChatStateAction, chatStateActionTypes } from "../../Action/Chat.State.Action";
import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";
import { LogManager } from "../Log/LogManager";
import { EjabberdConstants } from "./EjabberdConstants";
import { StanzaHelper } from "./StanzaHelper";
import { XmppCreds } from "./EjabberdConnectionManager";

export class OutgoingMessageManager {

    /**
     * The manager's access to the application store.
     */
    private storeAccess: StoreAccessManager;

    /**
     * The object's access to Strophe functionality.
     */
    private xmpp: IStrophe;

    /**
     * The classes access to the logging interface.
     */
    private logger: LogManager;

    /**
     * Check for archived query.
     */
    private hasQueriedArchive: boolean;

    /**
     * Returns a new instance of the outgoing messaging manager.
     */
    constructor(xmpp: IStrophe, storeAccess: StoreAccessManager, logger: LogManager) {
        
        this.storeAccess = storeAccess;
        this.xmpp = xmpp;
        this.logger = logger;
        
        this.hasQueriedArchive = false;
    }
    
    /**
     * Mark the thread at threadIndex as read.
     * @param {number} threadIndex 
     */
    public setThreadRead(threadIndex: number) {
        if (threadIndex === null || threadIndex === undefined) {
            throw ("Thread index for thread to mark as read not provided");
        }
        let action: ChatStateAction = { type: chatStateActionTypes.THREAD_READ, payload: { threadIndex: threadIndex } };
        this.logger.log(`Thread at index ${threadIndex} marked as read`);
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Compose an XEP 33 stanza. I.e. write a message to one or more recipients.
     * @param {string[]} to - The list of jids to send the message to 
     * @param {string} message - The message content
     * @param {string} from - The jid of the user sending the message
     * @param {boolean} addCurrentUser - Whether or not to add an address field for the currently authenticated user. 
     */
    public composeXEP33Message(from: Participant, to: Participant[], message: string, threadId?: string): Builder {
        // For details on XEP 33 see: https://xmpp.org/extensions/xep-0033.html
        // Basically you just send a message to a server resource and specify the targets in an <address /> tags. Then message content still goes in a <body />
        let id = uuidv4();
        // <message> </message>
        let stanza =
            this.xmpp.$msg({
                to: EjabberdConstants.XEP33ResourceLocation,
                from: from.jid.toLowerCase(),
                xmlns: EjabberdConstants.MessageXMLNS,
                type: EjabberdConstants.chat,
                id: id
            });

        stanza.c("nick", { xmlns: EjabberdConstants.nicknameXMLNS })
            .t(from.name);
        stanza.up();

        if (threadId) {
            stanza.c("thread").t(threadId);
            stanza.up();
        }

        // <addresses> </addresses>
        stanza.c("addresses", { xmlns: EjabberdConstants.XEP33XMLNS });
        this.addAddresses(stanza, to);
        stanza.up();

        // <body></body>
        stanza.c("body");
        stanza.t(message);
        stanza.up();

        // <headers></headers>
        stanza.c("headers", { xmlns: EjabberdConstants.shimXMLNS });
        stanza.c("header", { name: "Created" }, new Date().toISOString());
        stanza.up();

        // <request />
        this.addDeliveryReceiptRequest(stanza);
        return stanza;
    }

    /**
     * TODO: User object is not being used right now, save for refactoring.
     * For usage with XEP 33 compliant messaging
     * Add the passed list of jids as <address /> tags to the passed stanza.
     * @param {Builder} stanza - The stanza to add the addresses to
     * @param {string[]} to - A list of addresses to be added to
     * @param {boolean} addCurrentUser - Whether or not the current user should be added to the list of addresses
     */
    private addAddresses(stanza: Builder, to: Participant[]) {
        to.forEach((to) => {
            stanza.c("address", { type: "to", jid: to.jid.toLowerCase(), desc: to.name })
                .up();
        });
        return stanza;
    }

    /**
     * Adds a delivery receipt request to the stanza
     * @param {Builder} stanza - The stanza on which to request a delivery receipt
     * @returns {Builder} - The stanza with the delivery receipt request added. 
     */
    private  addDeliveryReceiptRequest(stanza: Builder): Builder {
        stanza.c("request", { xmlns: EjabberdConstants.deliveryReceiptXMLNS })
            .up();
        return stanza;
    }

    /**
     * Send the initial presence stanza
     */
    public sendInitialPresence(conn: Connection) {
        let initialPres = this.xmpp.$pres();
        this.sendStanza(conn, initialPres);
    }

    /**
     * Send a stanza to enable carbons
     */
    public enableCarbons(conn: Connection, connectionCredentials: XmppCreds ) {
        let carbonsStanza = this.xmpp.$iq({ xmlns: EjabberdConstants.MessageXMLNS, from: connectionCredentials.jid.toLowerCase(), id: uuidv4(), type: 'set' });
        carbonsStanza.c("enable", { xmlns: EjabberdConstants.carbonsXMLNS, queryid: uuidv4() });
        this.sendStanza(conn, carbonsStanza);
    }

    /**
     * Query the message archives to gather the user's message history
     */
    public queryArchives(conn: Connection) {
        if (!this.hasQueriedArchive) {
            // HACK? - I mean technically this is correct but it feels wrong
            this.hasQueriedArchive = true;
            let archivesStanza = this.xmpp.$iq({ type: 'set', xmlns: EjabberdConstants.MessageXMLNS, id: uuidv4() });
            archivesStanza.c("query", { queryid: uuidv4(), xmlns: EjabberdConstants.archivesXMLNS });
            this.sendStanza(conn, archivesStanza);
        }
    }

    public queryArchiveAfter(conn: Connection, stanza: Element): void {
        let iqstanza = this.xmpp.$iq({ type: "set", id: uuidv4() });
        iqstanza
            .c("query", { xmlns: EjabberdConstants.archivesXMLNS, queryid: uuidv4() })
            .c("set", { xmlns: EjabberdConstants.rsmXMLNS })
            .c("after")
            .t(StanzaHelper.getLastElementTextContent(stanza))
            .up()
            .up()
            .up();
        this.sendStanza(conn, iqstanza);
    }

    /**
     * Send a stanza to the ejabberd server
     * @param {Builder} stanza - The stanza to be sent
     */
    public sendStanza(conn: Connection, stanza: Builder1): void {
        this.logger.info(`Sending a stanza:\n${stanza.tree().outerHTML}`);
        conn.send(stanza);
    }
    
}


