/*----------  React  ----------*/
import * as React from "react";

/*----------  External libraries (https://www.npmjs.com/package/sweetalert-react)  ----------*/
import SweetAlert from "Sweetalert-React";


// Additional properties: http://t4t5.github.io/sweetalert/
interface EjabberdConnectivityContainerProps {
    show: boolean,
    title: string,
    text: string,
    type: string,
    showCloseButton: boolean,
    showCancelButton: boolean,
    showConfirmButton: boolean
}

// State
interface EjabberdConnectivityContainerState {
    show: boolean
}

export class EjabberdConnectivityMessenger extends React.Component<EjabberdConnectivityContainerProps, EjabberdConnectivityContainerState> {
    constructor(props) {
        super(props);
    }

    /**
     * Render the SweetAlert Connectivity Blocker, this probably could be more generic to be used all over, for now its only used for the blocking of loss of connectivity. yay!
     */
    public render() {
        /**
         * Return the sweetalert that is specific for the ejabberd connectivity, nothing else. The reusability is not needed for this.
         */
        return (
            <SweetAlert
                show={this.props.show}
                title={this.props.title}
                text={this.props.text}
                type={this.props.type}
                showCloseButton={this.props.showCloseButton}
                showCancelButton={this.props.showCancelButton}
                showConfirmButton={this.props.showConfirmButton}
            />
        )
    }

    /**
     * Update the component show status on render.
     * @param nextProps - Gets the new properties.
     * @returns {ejabberdConnection} - The ejabberd state from the redux store.
     */
    public shouldComponentUpdate(nextProps: EjabberdConnectivityContainerProps) {
        return this.determineIfComponentIsPresent(nextProps);
    }

    /**
     * Update the component show status on render.. 
     * @param nextProps - Gets the new properties.
     * @returns {ejabberdConnection} - The ejabberd state from the redux store.
     */
    public componentWillReceiveProps(nextProps: EjabberdConnectivityContainerProps) {
        return this.determineIfComponentIsPresent(nextProps);
    }

    /**
     * Simple method to not use the same code in two methods.
     * @param nextProps - Gets the new properties.
     * @returns {ejabberdConnection} - The ejabberd state from the redux store.
     */
    private determineIfComponentIsPresent(nextProps: EjabberdConnectivityContainerProps) {
        return this.props.show !== nextProps.show;
    }
}


