import { createBrowserHistory, History, LocationDescriptorObject } from "history";

// TODO - This is a horrible routing strategy, this needs to change before initial release.
export const routeMap = {
    Login: "/web-messaging/",
    Messages: "/web-messaging/"
};

// History needs to be injected just like the oidc bits
// Putting off writing the spec for when I refactor this manager
export class RouteManager {
    private history: History;

    constructor() {
        this.createBrowserHistory();
    }

    public getHash() {
        return this.history.location.hash;
    }
    
    public getPath() {
        return this.history.location.pathname;
    }
    
    public getQueryString() {
        return this.history.location.search;
    }

    public getHistoryState() {
        return this.history.location.state;
    }

    /**
     * Push the descriptors path name onto the history passing in the state and query string provided by the consumer.
     * @param descriptor The location descriptor object to be pushed onto the history
     */
    public navigateToRouteWithState(descriptor: LocationDescriptorObject): void {
        this.history.push(descriptor);
    }

    /**
     * Change the URL to match the passed path.
     * @param path The path to navigate to.
     */
    public navigateToRoute(path: string): void {
        this.history.push(path);
    }

    private createBrowserHistory() {
        this.history = createBrowserHistory();
    }
}


