import { EjabberdConnection } from "../../State/EjabberdConnectivity.State";
import { EjabberdConnectivityStateAction, ejabberdConnectivityStateActionTypes } from "../../Action/EjabberdConnectivity.State.Action";
import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";

export class ConnectivityManager {
    public ejabberdConnection: EjabberdConnection;
    public storeAccess: StoreAccessManager;

    constructor(ejabberdConnection: EjabberdConnection, storeAccess: StoreAccessManager) {
        this.ejabberdConnection = ejabberdConnection;
        this.storeAccess = storeAccess;
    }

    public setConnectionState(ejabberdConnection: EjabberdConnection) {
        let action: EjabberdConnectivityStateAction = {
            type: ejabberdConnectivityStateActionTypes.ESTABLISHED_CONNECTION, 
            payload: {isConnected: ejabberdConnection}
        };
        
        this.storeAccess.dispatchAction(action);
    }

    public getConnectionState() {
        return this.ejabberdConnection.connected;
    }

     /**
     * A callback function for the initial connection setup. Toasts the user as important changes take place.
     * @param {Strophe.Status} status - The status of the connection. 
     * @param {string} condition - An optional error condition string providing more detailed explanation of what if anything went wrong. 
     */
    public connectivityStatus(status: Strophe.Status, condition: string) {
        switch (status) {
           case Strophe.Status.ATTACHED:
           case Strophe.Status.AUTHENTICATING:
           case Strophe.Status.AUTHFAIL:
           case Strophe.Status.CONNECTING:
           case Strophe.Status.CONNFAIL:
           case Strophe.Status.DISCONNECTED:
           case Strophe.Status.DISCONNECTING:
           case Strophe.Status.ERROR:
           case Strophe.Status.REDIRECT:
                this.ejabberdConnection.connected = false;
            break;
            case Strophe.Status.CONNECTED:
                this.ejabberdConnection.connected = true;
                break;
            default:
                this.ejabberdConnection.connected = false;
                break;
        }
    }
}


