/*----------  Managers  ----------*/
import { services } from "../Managers/ServiceManager";
import { routeMap } from "../Managers/Routing/RouteManager";
import { ConfigurationEndPointResponse } from "../Managers/Configuration/ConfigurationManager";

/*----------  Redux related items  ----------*/
import { ProfileStateAction, profileStateActionTypes } from "../Action/Profile.State.Action";
import { EjabberdConnectivityStateAction, ejabberdConnectivityStateActionTypes } from "../Action/EjabberdConnectivity.State.Action";
import { AudibleSoundsStateAction, audibleSoundsStateActionTypes } from "../Action/AudibleSounds.State.Action";

/*----------  Constants  ----------*/
import { Sound_Config } from "../Config/App.Constants";
/*----------  State  ----------*/
import { AudibleSoundStates } from "../State/Notification-Sounds/AudibleSounds.State";

const clientAnchorLocation = "messagingClientAnchor";

let setInitialAudibleSounds = () => {
    let action: AudibleSoundsStateAction = {
        type: audibleSoundsStateActionTypes.SET_SOUND_PRESENCE,
        payload: {
            audibleSounds: {
                CurrentSound: Sound_Config.sound_path,
                PlayStatus: AudibleSoundStates.STOPPED,
                Position: 0,
                Volume: 0,
                HasPlayed: false
            }
        }
    };

    services.storeAccess.dispatchAction(action);
}

let resolveToken = () => {
    let token = services.storeAccess.getAccessToken();
    let base64Url = token.split('.')[1];
    let base64 = base64Url.replace('-', '+').replace('_', '/');
    let parsed = JSON.parse(window.atob(base64));

    let action: ProfileStateAction = {
        type: profileStateActionTypes.SET_PROFILE,
        payload: {
            profileData: {
                isRoot: false,
                jabberId: parsed.JID.toLowerCase(),
                userId: null,
                affiliations: [],
            }
        }
    };

    services.storeAccess.dispatchAction(action);
};

let setAppTitle = () => {
    services.appTitleManager.setDefaultTitle();
}

let getFeatureFlags = () => {
    return services.configurationManager.getFeatureFlags().then(response => {
        services.featureManager.resolveFeatureFlags(response);
    });
};

/* Initial load of the application for the ejabberd connectivity component. */
let setInitialEjabberdConnectivity = () => {
    let connAction: EjabberdConnectivityStateAction = {
        type: ejabberdConnectivityStateActionTypes.ESTABLISHED_CONNECTION,
        payload: {
            isConnected: {
                connected: false,
                showBlocker: false,
                initialLoad: true
            }
        }
    };
    services.storeAccess.dispatchAction(connAction);
};

let connectToEjabberdAndRenderApp = () => {
    /* NOTE: Keep this in for now, just to keep access-token out of URL. */
    services.router.navigateToRoute(routeMap.Messages);

    let xmppCreds = services.storeAccess.getXMPPCredentials();
    let massagedCreds = { name: xmppCreds.name, xmppServerUrl: xmppCreds.uri, pw: xmppCreds.accessToken, jid: xmppCreds.jid }
    services.ejabberdConnectionManager.connect(massagedCreds);
    services.renderer.renderApp(services.router,
        services.incomingMessageManager,
        services.outgoingMessageManager,
        services.ejabberdConnectionManager,
        services.searchManager,
        services.messagesUiManager,
        services.storeAccess,
        clientAnchorLocation,
        services.logger,
        services.connectivityManager,
        services.featureManager,
        services.audibleSoundsManager,
        services.appTitleManager);
};

export const bootstrap = (value: ConfigurationEndPointResponse) => {
    services.renderer.renderLoadingScreen(clientAnchorLocation);
    services.configurationManager.storeConfigurationItems(value.data.configurationItems);
    /* Case: the user is not yet logged in. Result: Redirect to the identity server login page. Control taken away from the web messaging client. */
    if (!services.router.getHash()) {
        services.oidcManager.signin().catch((error) => {
            // CQ JMR 07/05/2017 - If something blows up during the redirect I don't know what we can do. Hopefully this will be corrected if/when we ditch Brock's oidc implementation.
            services.renderer.renderErrorPage(clientAnchorLocation, error.message);
        });
    }
    /* Case: the user is already logged in. Result: Load the users authentication information and his/her profile information then prep the app. */
    else {
        return services.oidcManager.signinEnd().then(user => {
            services.oidcManager.loadAuthData(user);
        }).then(
        setAppTitle
        ).then(
        getFeatureFlags
        ).then(
        resolveToken
        ).then(
        setInitialEjabberdConnectivity
        ).then(
        setInitialAudibleSounds
        ).then(
        connectToEjabberdAndRenderApp
        ).catch(error => {
            // CQ JMR 07/05/2017 - Error page rendering and behavior should be discussed with UX.
            services.renderer.renderErrorPage(clientAnchorLocation, error.message);
        });
    }
};




