
import { Alert, AlertStates, AlertParticipant } from "../../State/Alert.State";
import { Thread, Participant } from "../../State/Chat.State";
import { MainPresenter } from "../../State/Messages.UI.State";
import { ChatStateAction, chatStateActionTypes } from "../../Action/Chat.State.Action";
import { AlertStateAction, alertStateActionTypes } from "../../Action/Alert.State.Action";
import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";
import { MessagesUiManager } from "../UI/MessagesUI.Manager";

import { LogManager } from "../Log/LogManager";
import { StanzaHelper } from "./StanzaHelper";

export class IncomingMessageManager {

    /**
     * The manager's access to the application store.
     */
    private storeAccess: StoreAccessManager;

    /**
     * The manager's access to the UI state it is concerned with.
     */
    private UiManager: MessagesUiManager;

    /**
     * The classes access to the logging interface.
     */
    private logger: LogManager;

    /**
     * Returns a new instance of the incoming messaging manager.
     */
    constructor(storeAccess: StoreAccessManager, uiManager: MessagesUiManager, logger: LogManager) {
        this.storeAccess = storeAccess;
        this.UiManager = uiManager;
        this.logger = logger;
    }

    /**
     * Create a new thread withe passed participants as its members, the passed messages as its contents, the passed unreadCount as its unread count,
     * and the passed id as its id. Pass true for activate to immediately tell the redux store that this 
     * @param participants 
     * @param messages 
     * @param unreadCount 
     * @param id 
     * @param activate 
     */
    public buildNewThread(participants: Participant[], messages: Element[], unreadCount: number, id: string, activate?: boolean): Thread {
        this.logger.log(`New thread initiated\nParticipants: ${participants}\nId: ${id}`);
        let thread = {

            messages: messages,
            participants: participants,
            unreadCount: unreadCount,
            id: id
        };

        let action: ChatStateAction = {
            type: chatStateActionTypes.CREATE_THREAD,
            payload: {
                thread: thread
            }
        };
        this.storeAccess.dispatchAction(action);
        return thread;
    }

    /**
     * Establishes the connection to the XMPP server using the connection credentials and sets up the event handlers
     * @returns {boolean} - True if successful, false otherwise
     */
    public addMessageToThread(message: Element, threadId: number) {
        let action: ChatStateAction = {
            type: chatStateActionTypes.THREAD_MESSAGE,
            payload: {
                threadIndex: threadId,
                threadMessage: message
            }
        };
        this.storeAccess.dispatchAction(action);
    }
    
    /**
     * Process an incoming message stanza
     * @param {Element} stanza - The message that was received from the strophe handler
     */
    public processIncomingMessage(stanza: Element): void {
        this.logger.info(`Stanza received:\n${stanza.outerHTML}`);

        let from = StanzaHelper.getJid(stanza);
        // DRY - Once new element in stanza is added for determination of alerts vs chat messages, update ternary below to not use spokbot, as this is a hack and is ugly!
        this.logger.log(`Parsing alert: ${stanza.outerHTML}`);
        let participants = from.toLowerCase() === 'spokbot@spokvdev.com' ? StanzaHelper.getAlertParticipantFromStanza(stanza) : StanzaHelper.getParticipantsFromStanza(stanza);
        let threadId = StanzaHelper.getThreadId(stanza);
        let threadIndex = this.storeAccess.checkForThreadId(threadId);

        // DRY - Make that logic of determining if is an archived message a method of its own
        // DRY - Make logic of nuking the resource a method of its own
        let isFromMe = (from.toLowerCase() === this.storeAccess.getCurrentUser().jid.toLowerCase());

        // Hack - This spokbot hack is for alerting, NEVER DO THIS! 
        if (from.toLowerCase() === "spokbot@spokvdev.com") {
            this.processAlertMessageForNewThread(stanza, participants, threadId);
        }
        else if (threadIndex === -1 && from.toLowerCase() !== 'spokbot@spokvdev.com') {
            this.processMessageForNewThread(stanza, participants, threadId, isFromMe);
        }
        else {
            this.processMessageForExistingThread(stanza, threadIndex, isFromMe);
        }
    }

    /**
     * 
     */
    public processMessageForNewThread(stanza: Element, participants: Participant[], threadId: string, isFromMe: boolean): void {
        let thread: Thread = {
            participants: participants,
            id: threadId,
            messages: [stanza],
            unreadCount: (isFromMe) ? 0 : 1
        };
        let action: ChatStateAction = {
            type: chatStateActionTypes.CREATE_THREAD,
            payload: {
                thread: thread
            }
        };

        this.storeAccess.dispatchAction(action);

        if (isFromMe) {
            this.UiManager.setActiveThread(thread);
            this.UiManager.updateMainPresenter(MainPresenter.thread);
        }
    }

    public processMessageForExistingThread(stanza: Element, threadIndex: number, isFromMe: boolean): void { //TODO refactor out the isFromMe. That's dangerously interdependent.
        let bodyContent = stanza.getElementsByTagName("body")[0];
        if (bodyContent) {
            // Add the message to that thread
            let action: ChatStateAction = {
                type: chatStateActionTypes.THREAD_MESSAGE,
                payload: {
                    threadIndex: threadIndex,
                    threadMessage: stanza
                }
            };
            this.storeAccess.dispatchAction(action);

            // If there is an active thread and I am in it
            if (this.storeAccess.getActiveThread()) {
                if (this.storeAccess.getActiveThread().id === this.storeAccess.getThreadByIndex(threadIndex).id) {
                    this.UiManager.setActiveThread(this.storeAccess.getThreadByIndex(threadIndex));
                    this.UiManager.updateMainPresenter(MainPresenter.thread);
                }
            }
            // If I sent the message update the thread
            else if (isFromMe) {
                this.UiManager.setActiveThread(this.storeAccess.getThreadByIndex(threadIndex));
                this.UiManager.updateMainPresenter(MainPresenter.thread);
            }
            else {
                // Don't do anything I think. 
            }
        }
    }

    /**
     *  This could probably be moved into its own manager, for now, this is just the temporary fix that actually works and doesn't hard code logic on the presentation layer.
     */
    public processAlertMessageForNewThread(stanza: Element, alertMembers: { name: string, jid: string }[], alertId: string) {
        let participants: AlertParticipant[] = [];
        alertMembers.forEach(member => { participants.push({ jid: member.jid.toLowerCase(), name: member.name, state: AlertStates.UnRead }) });
        let alert: Alert = {
            participants: participants,
            id: alertId,
            messages: [stanza],
            unreadCount: 0 //hard coded for now, only POC, but this is not only a POC - HUH.
        };
        let action: AlertStateAction = {
            type: alertStateActionTypes.CREATE_ALERT,
            payload: {
                alert: alert
            }
        };

        // Throwing that alert to the store.
        this.storeAccess.dispatchAction(action);
    }
}


