import { store } from "../Store/App.Store";
import { concreteStrophe } from "../Injectables/Strophe.Injectable";
import { concreteAxios, IHttpRequester } from "../Injectables/Axios.Injectable";
import { EjabberdConnection } from "../State/EjabberdConnectivity.State";

import { ConfigurationManager, EnvironmentConfiguration } from "./Configuration/ConfigurationManager";
import { FeatureManager } from "./FeatureManagement/FeatureManager";
import { OidcManager, OidcConfig } from "./IdentityServer/OidcManager";
import { LogManager } from "./Log/LogManager";
import { ConnectivityManager } from "./NetworkConnectivity/Connectivity.Manager";
import { ProfileManager, ProfileConfiguration } from "./Profile/ProfileManager";
import { RenderingManager } from "./Rendering/RenderingManager";
import { RouteManager } from "./Routing/RouteManager";
import { SearchManager, SearchConfig } from "./Search/SearchManager"; // I feel like moving the search config type into a separate file is over kill, but that import looks really weird
import { StringManipulationManager } from "./StringManipulation/StringManipulationManager";
import { StoreAccessManager } from "./StoreAccess/StoreAccessManager";
import { EjabberdUiManager } from "./UI/Connectivity/Ejabberd.UI.Manager";
import { MessagesUiManager } from "./UI/MessagesUI.Manager";
import { IncomingMessageManager } from "./XMPP/IncomingMessageManager";
import { OutgoingMessageManager } from "./XMPP/OutgoingMessageManager";
import { EjabberdConnectionManager } from "./XMPP/EjabberdConnectionManager";
import { AudibleSoundsManager } from "./AudibleSounds/AudibleSounds.Manager";
import { AppTitleManager } from "../Components/AppTitle/AppTitle.Manager";

// Environment configuration comes from env.js which is NOT bundled. 
declare let window: { __SpokEnv: EnvironmentConfiguration, atob: any };

function configurationManagerFactory(storeAccess: StoreAccessManager, envConfig: EnvironmentConfiguration, httpRequester: IHttpRequester) {
    return new ConfigurationManager(storeAccess, envConfig, httpRequester);
}

function featureManagerFactory(storeAccess: StoreAccessManager) {
    return new FeatureManager(storeAccess);
}

function oidcManagerFactory(config: OidcConfig, storeAccess: StoreAccessManager) {
    return new OidcManager(config, storeAccess);
}

function logManagerFactory(configManager: ConfigurationManager, console: Console) {
    return new LogManager(configManager, console);
}

function connectivityManagerFactory(ejabberdConnection: EjabberdConnection, storeAccess: StoreAccessManager) {
    return new ConnectivityManager(ejabberdConnection, storeAccess);
}

function profileManagerFactory(config: ProfileConfiguration, storeAccess: StoreAccessManager) {
    return new ProfileManager(config, storeAccess);
}

function renderingManagerFactory() {
    return new RenderingManager();
}

function audibleSoundsManagerService(storeAccess: StoreAccessManager) {
    return new AudibleSoundsManager(storeAccess);
}

function appTitleManagerService(storeAccess: StoreAccessManager, uiManager: MessagesUiManager) {
    return new AppTitleManager(storeAccess, uiManager);
}

function routeManagerFactory() {
    return new RouteManager();
}

function searchManagerFactory(storeAccess: StoreAccessManager, config: SearchConfig, httpRequester: IHttpRequester, logger: LogManager) {
    return new SearchManager(storeAccess, config, httpRequester, logger);
}

function storeAccessManagerFactory() {
    return new StoreAccessManager(store);
}

function ejabberdUiManagerFactory(storeAccess: StoreAccessManager) {
    return new EjabberdUiManager(storeAccess);
}

function messagesUiManagerFactory(storeAccess: StoreAccessManager) {
    return new MessagesUiManager(storeAccess);
}

function incomingMessageManagerFactory(
    storeAccess: StoreAccessManager,
    uiManager: MessagesUiManager,
    logger: LogManager) {
    return new IncomingMessageManager(storeAccess,
        uiManager,
        logger);
}

function outgoingMessageManagerFactory(
    storeAccess: StoreAccessManager,
    logger: LogManager) {
    return new OutgoingMessageManager(concreteStrophe,
        storeAccess,
        logger);
}

function ejabberdConnectionManagerFactory(
    storeAccess: StoreAccessManager,
    uiManager: MessagesUiManager, logger: LogManager,
    connectivityManager: ConnectivityManager,
    ejabberdConnection: EjabberdConnection,
    incomingMessageManager: IncomingMessageManager,
    outgoingMessageManager: OutgoingMessageManager) {
    return new EjabberdConnectionManager(concreteStrophe,
        storeAccess,
        logger,
        connectivityManager,
        ejabberdConnection,
        incomingMessageManager,
        outgoingMessageManager);
}

function stringManagerFactory() {
    return new StringManipulationManager();
}

/**
 * Build an instance of all the service classes and store them in an object.
 */
function serviceFactory() {
    let storeAccess = storeAccessManagerFactory();
    let renderer = renderingManagerFactory();
    let router = routeManagerFactory();
    let stringManager = stringManagerFactory();
    let ejabberdUiManager = ejabberdUiManagerFactory(storeAccess);
    let featureManager = featureManagerFactory(storeAccess);
    let configurationManager = configurationManagerFactory(storeAccess, window.__SpokEnv, concreteAxios);
    let logger = logManagerFactory(configurationManager, console);
    let searchManager = searchManagerFactory(storeAccess, configurationManager.envConfig.messagingApi, concreteAxios, logger);
    let messagesUiManager = messagesUiManagerFactory(storeAccess);
    let oidcManager = oidcManagerFactory(configurationManager.envConfig.oidc, storeAccess);
    let connectivityManager = connectivityManagerFactory(null, storeAccess);
    let incomingMessageManager = incomingMessageManagerFactory(storeAccess, messagesUiManager, logger);
    let outgoingMessageManager = outgoingMessageManagerFactory(storeAccess, logger);
    let ejabberdConnectionManager = ejabberdConnectionManagerFactory(storeAccess,
        messagesUiManager,
        logger,
        connectivityManager,
        null,
        incomingMessageManager,
        outgoingMessageManager);
    const audibleSoundsManager = audibleSoundsManagerService(storeAccess);
    const appTitleManager = appTitleManagerService(storeAccess, messagesUiManager);

    return {
        logger: logger,
        storeAccess: storeAccess,
        renderer: renderer,
        router: router,
        stringManager: stringManager,
        ejabberdUiManager: ejabberdUiManager,
        featureManager: featureManager,
        configurationManager: configurationManager,
        searchManager: searchManager,
        incomingMessageManager: incomingMessageManager,
        outgoingMessageManager: outgoingMessageManager,
        messagesUiManager: messagesUiManager,
        oidcManager: oidcManager,
        connectivityManager: connectivityManager,
        ejabberdConnectionManager: ejabberdConnectionManager,
        audibleSoundsManager: audibleSoundsManager,
        appTitleManager: appTitleManager
    };
}

export const services = serviceFactory();

export const handleStropheLog = (level: number, message: string) => {
    switch (level) {
        case 4:
            services.logger.error(`Strophe fatal encountered. All stanza handlers will be dropped. Attempting to reconnect.\n\n${message}`);
            // DRY JMR 07/06/2017 - Should either provide this as a method or just restructure the return values from the store access so that the property names are the same.
            let xmppCreds = services.storeAccess.getXMPPCredentials();
            let massagedCreds = { name: xmppCreds.name, xmppServerUrl: xmppCreds.uri, pw: xmppCreds.accessToken, jid: xmppCreds.jid };
            services.ejabberdConnectionManager.connect(massagedCreds);
            break;
        case 3:
            // CQ JMR 07/06/2017 - Provide more detailed implementation after investigating what Jack considers an "error" and what he considers a "warning".
            services.logger.error(`Strophe error encountered.\n\n${message}`);
            break;
        case 2:
            services.logger.warn(`Strophe has thrown a warning:\n\n${message}`);
            break;
        default:
            services.logger.info(`Strophe has encountered a level ${level} message.\n\n${message}`)
    }
}


