/*----------  External libraries  ----------*/
import * as React from "react";
import * as ReactDOM from "react-dom";
import { addFeatureFlags } from 'react-redux-feature-flags';

/*----------  Managers  ----------*/
import { services } from "./Managers/ServiceManager";

import { bootstrap } from "./_OnStart/ApplicationBootstrap";

const clientAnchorLocation = "messagingClientAnchor";


/*----------  Get app configuration endpoints from the messaging endpoints  ----------*/
services.configurationManager.getMessagingApiConfigurationItems()
.then(bootstrap)
.catch((error) => { 
    services.renderer.renderErrorPage(clientAnchorLocation);
    services.logger.error(`Fatal application error encountered:\n\n ${error}`);
});




