/*----------  React & Redux  ----------*/
import * as React from "react";
import { Unsubscribe } from "redux";

/*----------  States  ----------*/
import { MainPresenter, SidebarTab } from "../../State/Messages.UI.State";
import { PersonSearchResult } from "../../State/PeopleSearch.State";
import { EjabberdConnection } from "../../State/EjabberdConnectivity.State";
import { Alert } from "../../State/Alert.State";
import { Thread, Participant } from "../../State/Chat.State";
import { FeatureFlag } from "../../State/FeatureFlags/FeatureFlags.State";

/*----------  Managers  ----------*/
import { SearchManager } from "../../Managers/Search/SearchManager";
import { MessagesUiManager } from "../../Managers/UI/MessagesUI.Manager";
import { IncomingMessageManager } from "../../Managers/XMPP/IncomingMessageManager";
import { OutgoingMessageManager } from "../../Managers/XMPP/OutgoingMessageManager";
import { EjabberdConnectionManager } from "../../Managers/XMPP/EjabberdConnectionManager";

import { StanzaHelper } from "../../Managers/XMPP/StanzaHelper";
import { StoreAccessManager } from "../../Managers/StoreAccess/StoreAccessManager";
import { ConnectivityManager } from "../../Managers/NetworkConnectivity/Connectivity.Manager";
import { LogManager } from "../../Managers/Log/LogManager";
import { FeatureManager } from "../../Managers/FeatureManagement/FeatureManager";
import { AudibleSoundsManager } from "../../Managers/AudibleSounds/AudibleSounds.Manager";
import { AppTitleManager } from "../../Components/AppTitle/AppTitle.Manager";

/*----------  Presenters  ----------*/
import { Icon } from "../../Components/Icons/Icon";
import { MessagesSideBar } from "./Presenters/Messages.Sidebar.Presenter";
import { MessagesMain } from "./Presenters/Messages.Main.Presenter";
import { EjabberdConnectivityMainMessenger } from "./Presenters/Components/Notifications/EjabberdConnectivity.Main.Presenter";
import { AppTitle } from "../../Components/AppTitle/AppTitle";

interface MessagesContainerProps {
    peopleSearcher: SearchManager;
    uiManager: MessagesUiManager;
    incomingMessageManager: IncomingMessageManager;
    outgoingMessageManager: OutgoingMessageManager;
    ejabberdConnectionManager: EjabberdConnectionManager;
    storeAccess: StoreAccessManager;
    logger: LogManager;
    featureManager: FeatureManager;
    connectivityManager: ConnectivityManager;
    audibleSoundsManager: AudibleSoundsManager;
    appTitleManager: AppTitleManager;
}

export interface MessagesContainerState {
    mainPresenter: MainPresenter;
    activeSidebarTab: SidebarTab;
    peopleSearchResults: PersonSearchResult[];
    currentRecipients: PersonSearchResult[];
    threads: Thread[];
    activeThread: Thread;
    activeAlert: Alert;
    featureFlag: FeatureFlag;
    serverConnected: EjabberdConnection;
    alerts: Alert[];
    appTitle: string;
}

export class MessagesContainer extends React.Component<MessagesContainerProps, MessagesContainerState> {
    private unsub: Unsubscribe;

    /**
     * Returns a new instance of the messages container.
     * @param {MessagesContainerProps} props - The properties passed from the parent component.
     */
    constructor(props) {
        super(props);

        this.state = {
            mainPresenter: MainPresenter.empty,
            activeSidebarTab: SidebarTab.threads,
            peopleSearchResults: [],
            currentRecipients: [],
            threads: [],
            serverConnected: null,
            activeThread: null,
            activeAlert: null,
            featureFlag: null,
            alerts: [],
            appTitle: "Messages"
        };

    }

    public render() {
        return (
            <div className="flexContainer">

                <AppTitle
                    title={this.state.appTitle}
                    unreadThreads={this.props.storeAccess.getUnreadThreadsExcludingActive()}
                    appTitleManager={this.props.appTitleManager} />

                <EjabberdConnectivityMainMessenger
                    show={this.showXMPPErrorMessage()}
                    url={this.props.storeAccess.getAudibleSounds().Sound.CurrentSound}
                    storeAccess={this.props.storeAccess}
                    audibleSoundsManager={this.props.audibleSoundsManager} />

                <nav>
                    <div className="colHeader">&nbsp;</div>
                    <div id="myProfile">
                        <div className="userAvatar"
                            style={{ backgroundImage: 'url(https://api.adorable.io/avatars/100/baboon.png)' }}></div>
                    </div>
                    <ul>
                        <li id="messagesNav" className="active" data-unreadMessageCount="0">
                            <Icon
                                dataQa="messages_bubble"
                                identifier="messageBubble"
                                styles="icon-messages-bubble"
                            />&nbsp;
                            <span>Messages</span>
                        </li>
                        {/* <li><i className="icon-thumbs_up_down"></i><span>Feedback</span></li>
                        <li><i className="icon-exit_to_app"></i><span>Logout</span></li> */}
                    </ul>
                </nav>
                <MessagesSideBar
                    activeTab={this.state.activeSidebarTab}
                    changeActiveTab={(sidebar: SidebarTab) => this.dispatchTabUpdate(sidebar)}
                    changeMainPresenter={(main: MainPresenter) => this.dispatchMainPresenterUpdate(main)}
                    threads={this.state.threads}
                    alerts={this.state.alerts}
                    activeThread={this.state.activeThread}
                    activeAlert={this.state.activeAlert}
                    changeActiveThread={(thread: Thread) => {
                        this.updateActiveThread(thread);
                    }}
                    changeActiveAlert={(alert: Alert) => {
                        this.updateActiveAlert(alert);
                    }}
                    user={this.props.storeAccess.getCurrentUser()}
                    featureFlags={this.props.storeAccess.getFeatFlags()}
                    uiManager={this.props.uiManager} />



                <MessagesMain
                    presenter={this.state.mainPresenter}
                    changeMainPresenter={(main: MainPresenter) => {
                        this.dispatchMainPresenterUpdate(main);
                    }}
                    user={this.props.storeAccess.getCurrentUser()}
                    search={(searchTerm: string) => {
                        this.dispatchPeopleSearch(searchTerm);
                    }}
                    people={this.state.peopleSearchResults}
                    recipients={this.state.currentRecipients}
                    updateRecipients={(recipients: PersonSearchResult[]) => {
                        this.updateRecipients(recipients);
                    }}
                    sendMessage={(to: Participant[], message: string) => {
                        this.sendMessage(to, message);
                    }}
                    updateMessageText={(messageText: string) => {
                        this.updateMessageText(messageText);
                    }}
                    clearMessageContents={() => {
                        this.clearMessageContents();
                    }}
                    thread={this.state.activeThread}
                    alert={this.state.activeAlert}
                    uiManager={this.props.uiManager} />

            </div>
        );
    }

    /**
     * Establish the subscription to the redux store.
     * @returns {Unsubscribe} - The callback to be invoked after the component unmounts/unsubscribes from the store.
     */
    public componentDidMount() {
        this.unsub = this.props.storeAccess.subscribe(() => {
            // NOTE: This is somewhat dangerous as it relies on the properties returned from the store access having
            // the same property names as the container.
            const subData = this.props.storeAccess.getMessagesContainerSubData();
            this.setState(subData);
        });
    }

    /**
     * Unsubscribe from the redux store when the component unmounts.
     */
    public componentWillUnmount() {
        this.unsub();
    }

    /**
     * Update the active thread for chat.
     */
    public updateActiveThread(thread: Thread) {
        thread.unreadCount = 0;
        this.props.uiManager.setActiveThread(thread);
        this.props.uiManager.updateMainPresenter(MainPresenter.thread);
    }

    /**
     * Update the ejabberd notification blocker.
     * @returns {boolean} - The ability to enable and disable network notifications.
     */
    private showXMPPErrorMessage() {
        return this.state.serverConnected ? this.state.serverConnected.showBlocker : false;
    }

    /**
     * Update the active alert for alerting.
     * @param {Alert} alert - The alert state object.
     */
    public updateActiveAlert(alert: Alert) {
        alert.unreadCount = 0;
        this.props.uiManager.setActiveAlert(alert);
        this.props.uiManager.updateMainPresenter(MainPresenter.alert);
    }

    /**
     * Dispatch an action to update the currently active UI sidebar tab to the passed tab value.
     * @param {SidebarTab} tab - The new tab to be focused
     */
    private dispatchTabUpdate(tab: SidebarTab) {
        this.props.uiManager.updateSideBarTab(tab);
    }

    /**
     * Update the main UI presenter to the passed presenter value.
     * @param {MainPresenter} main - The new main presenter
     */
    private dispatchMainPresenterUpdate(main: MainPresenter) {
        if (main !== MainPresenter.thread) {
            this.props.uiManager.setActiveThread(null);
        }
        this.props.uiManager.updateMainPresenter(main);
    }

    /**
     * Update the list of recipients to the passed list of recipients.
     * @param {PersonSearchResult[]} recipients - The new list of recipients
     */
    private updateRecipients(recipients: PersonSearchResult[]) {
        this.props.uiManager.updateRecipients(recipients);
    }

    /**
     * Dispatch a search to the messaging search API endpoint with the passed query.
     * @param {string} query - The search term
     */
    private dispatchPeopleSearch(query: string) {
        // Issue a search passing in the current access token and the query,
        // then resolve the promise that gets returned to dispatch action to update the store.
        const token = this.props.storeAccess.getAccessToken();
        this.props.peopleSearcher.resolveSearch(
            this.props.peopleSearcher.search(token, query),
        );
    }

    /**
     * Send a message to the passed list of people with the passed message as the body
     * @param {PersonSearchResult[]} to - The addresses to send the message to
     * @param {string} message - The message to be sent. If not specified, send the content of the redux store's message
     * text.
     */
    private sendMessage(to: Participant[], message: string) {
        if (!message) {
            // TODO remove this after refactoring the compose screen.
            message = this.props.storeAccess.getMessageText();
        }
        if (!to) {
            this.props.logger.warn("To was passed as null");
            return;
        }
        this.props.logger.info(`Send message invoked\nTo:${JSON.stringify(to)}\nMessage: ${message}`);
        const creds = this.props.storeAccess.getXMPPCredentials();
        const from: Participant = { jid: creds.jid.toLowerCase(), name: creds.name };

        let threadId = "";
        const sortedParticipants = StanzaHelper.sortParticipants(to.concat(from));
        sortedParticipants.forEach((participant) => {
            threadId += participant.jid.toLowerCase();
        });
        const stanza = this.props.outgoingMessageManager.composeXEP33Message(from, to, message);
        const threadIndex = this.props.storeAccess.checkForThreadId(threadId);

        // Send a message to a thread that already exists --> i.e. just append the new message to the thread
        if (threadIndex !== -1) {
            this.props.incomingMessageManager.addMessageToThread(stanza.tree(), threadIndex);
            this.updateActiveThread(this.props.storeAccess.getThreadByIndex(threadIndex));
        } else {
            // Send a message to a thread that doesn't exist yet --> i.e. create a new thread with the message as the
            // first message in it.
            // Since it is us sending a message, make the thread the active thread
            const thread = this.props.incomingMessageManager.buildNewThread(sortedParticipants, [stanza.tree()], 0, threadId, true);
            this.updateActiveThread(thread);
        }
        this.props.outgoingMessageManager.sendStanza(this.props.ejabberdConnectionManager.connection, stanza);
    }

    /**
     * Update the redux store to set the message text.
     * @param {string} messageText - The new message text
     */
    private updateMessageText(messageText: string) {
        // NOTE: 04/17/2017 - These ui update methods are so short now we can probably recycle them and just pass the
        // managers behaviour directly.
        this.props.uiManager.updateMessageText(messageText);
    }

    /**
     * Clears the contents of the message text and recipient list in the redux store.
     */
    private clearMessageContents() {
        // NOTE: 04/17/2017 - These ui update methods are so short now we can probably recycle them and just pass the
        // managers behaviour directly.
        this.props.uiManager.clearMessageText();
    }

}



