
import { EjabberdConnection } from "../../State/EjabberdConnectivity.State";
import { Connection, IStrophe, connStatus } from "../../Injectables/Strophe.Interface";

import { AudibleSoundStates } from "../../State/Notification-Sounds/AudibleSounds.State";
import { ChatStateAction, chatStateActionTypes } from "../../Action/Chat.State.Action";
import { EjabberdConnectivityStateAction, ejabberdConnectivityStateActionTypes } from "../../Action/EjabberdConnectivity.State.Action";
import { AudibleSoundsStateAction, audibleSoundsStateActionTypes } from "../../Action/AudibleSounds.State.Action";

import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";
import { ConnectivityManager } from '../NetworkConnectivity/Connectivity.Manager';
import { AudibleSoundsManager } from "../AudibleSounds/AudibleSounds.Manager";

import { LogManager } from "../Log/LogManager";

import { EjabberdConstants } from "./EjabberdConstants";
import { IncomingMessageManager } from "./IncomingMessageManager";
import { OutgoingMessageManager } from "./OutgoingMessageManager";


export interface XmppCreds {
    xmppServerUrl: string;
    jid: string;
    pw: string;
    name: string;
}

export class EjabberdConnectionManager {

    /**
     * The sound manager, for having complete control of notification sounds.
     */
    private audibleSoundsManager: AudibleSoundsManager;

    /**
     * The manager's access to the network connectivity for the Ejabber server.
     */
    private ejabberdConnection: EjabberdConnection;

    /**
     * The manager's access to the network connectivity for the Ejabber server.
     */
    private connectivity: ConnectivityManager;

    /**
     * The manager's access to the application store.
     */
    private storeAccess: StoreAccessManager;

    /**
     * A manager for handling incoming messages. 
     */
    private incomingMessageManager: IncomingMessageManager;

    /**
    * A manager for handling outgoing messages. 
    */
    private outgoingMessageManager: OutgoingMessageManager;

    /**
     * The object that manages stanzas coming to and from the XMPP server.
     */
    public connection: Connection;

    /**
     * The object's access to Strophe functionality.
     */
    private xmpp: IStrophe;

    /**
     * Indicates the number of times a connection establishment has failed.
     */
    private connFailCircuitBreakerTick: number;

    /**
     * An object that contains the login credentials to be used for authentication to the XMPP server.
     */
    public connectionCredentials: XmppCreds;

    /**
     * The classes access to the logging interface.
     */
    private logger: LogManager;

    /**
     * Returns a new instance of the Ejabberd Connection manager.
     */
    constructor(
        xmpp: IStrophe,
        storeAccess: StoreAccessManager,
        logger: LogManager,
        connectivity: ConnectivityManager,
        ejabberdConnection: EjabberdConnection,
        incomingMessageManager: IncomingMessageManager,
        outgoingMessageManager: OutgoingMessageManager) {

        this.incomingMessageManager = incomingMessageManager;
        this.outgoingMessageManager = outgoingMessageManager;
        this.storeAccess = storeAccess;
        this.connectivity = connectivity;
        this.ejabberdConnection = ejabberdConnection;
        this.xmpp = xmpp;
        this.logger = logger;

        this.connFailCircuitBreakerTick = 0;
    }

    /**
     * Establishes the connection to the XMPP server using the connection credentials and sets up the event handlers
     * @returns {boolean} - True if successful, false otherwise
     */
    public connect(creds: XmppCreds): boolean {
        this.connectionCredentials = creds;
        this.connection = this.xmpp.buildConnection(this.connectionCredentials.xmppServerUrl);

        this.connection.connect(
            this.connectionCredentials.jid.toLowerCase(),
            this.connectionCredentials.pw,
            (status: number, condition: string) => {
                this.processConnectionStatus(status, condition);
            }
        );

        // Keep the current context visible in the handler methods.
        let mgr = this;

        let setUserAction: ChatStateAction = {
            type: chatStateActionTypes._SET_USER,
            payload: {
                user: this.connectionCredentials.jid.toLowerCase()
            }
        };
        this.storeAccess.dispatchAction(setUserAction);

        this.connection.addHandler((stanza) => {
            this.incomingMessageManager.processIncomingMessage(stanza);
            return true;
        }, null, "message", null);

        this.connection.addHandler((stanza) => {
            mgr.logger.info(`Presence stanza received:\n${stanza.outerHTML}`);
            return true;
        }, null, "presence", null);

        this.connection.addHandler((stanza) => {
            mgr.logger.info(`Iq stanza received:\n${stanza.outerHTML}`);

            // CQ JMR 07/20/2017 - The IQ handler should probably be dissected into separate handlers based on the XMPP spec that is handled by the stanza being received. Investigate opportunities.
            //                     Not a big issue yet, but the more specs we implement the more likely this will become a pain.
            if (mgr.shouldQueryArchiveAfter(stanza)) {
                this.outgoingMessageManager.queryArchiveAfter(this.connection, stanza);
            }
            return true;
        }, null, "iq", null);

        return true;
    }

    /**
    * Simple sleep method for the EJabberd timeout.
    */
    private sleep(milliseconds) {
        let start = new Date().getTime();

        for (let i = 0; i < 1e7; i++) {
            if ((new Date().getTime() - start) > milliseconds) {
                break;
            }
        }
    }

    /**
    * Ejabberd timeout to check for connection status.
    */
    private ejabberdTimeout() {
        let val = true;

        // Check for disconnected state from the process connection method.
        if (connStatus.DISCONNECTED) {
            while (val) {
                this.sleep(13000);
                if (connStatus.CONNECTED) {
                    val = false;
                } else {
                    val = true;
                }
            }
        }
    }

    /**
     * Updating the store for ejabberd connectivity to show the blocker and disable the blocker.
     */
    private updateSoundManager(playStatus: string) {
        let audibleSoundFromStore = this.storeAccess.getAudibleSounds();

        if (playStatus === AudibleSoundStates.PLAYING && audibleSoundFromStore.Sound.HasPlayed === true) {
            playStatus = AudibleSoundStates.STOPPED;
        } else if (playStatus === AudibleSoundStates.PLAYING && audibleSoundFromStore.Sound.HasPlayed === false) {
            playStatus = AudibleSoundStates.PLAYING;
        } else if (playStatus === AudibleSoundStates.STOPPED && audibleSoundFromStore.Sound.HasPlayed === true) {
            playStatus = AudibleSoundStates.STOPPED;

            if (this.getCurrentEjabberdStatus().showBlocker === false) {
                audibleSoundFromStore.Sound.HasPlayed = false;
            }
        } else if (playStatus === AudibleSoundStates.STOPPED && audibleSoundFromStore.Sound.HasPlayed === false) {
            playStatus = AudibleSoundStates.STOPPED;
        }

        let audibleSoundAction: AudibleSoundsStateAction = {
            type: audibleSoundsStateActionTypes.SET_SOUND_PRESENCE,
            payload: {
                audibleSounds: {
                    CurrentSound: audibleSoundFromStore.Sound.CurrentSound,
                    PlayStatus: playStatus,
                    Position: audibleSoundFromStore.Sound.Position,
                    Volume: audibleSoundFromStore.Sound.Volume,
                    HasPlayed: audibleSoundFromStore.Sound.HasPlayed
                }
            }
        };

        this.storeAccess.dispatchAction(audibleSoundAction);
    }

    private getCurrentEjabberdStatus() {
        return this.storeAccess.getEjabberdStatus().ejabberdConnection;
    }

    /**
     * Updating the store for ejabberd connectivity to show the blocker and disable the blocker.
     */
    private setEjabberdConnectivity() {
        let activity = this.storeAccess.getActiveConnection();
        if (activity.connected !== this.ejabberdConnection.connected) {
            let action: EjabberdConnectivityStateAction = {
                type: ejabberdConnectivityStateActionTypes.ESTABLISHED_CONNECTION
                , payload: { isConnected: this.ejabberdConnection }
            };

            this.storeAccess.dispatchAction(action);
        }
    }

    /**
    * Process the connection status from strophe.
    */
    private processConnectionStatus(status: number, condition: string): void {
        switch (status) {
            case connStatus.ERROR:
                this.ejabberdConnection = { connected: false, showBlocker: true, initialLoad: false };
                this.connFailCircuitBreakerTick += 1;
                this.setEjabberdConnectivity();
                /* Check if this is the first error/disconnection, if it's not, then we want the chime to sound. */
                this.connFailCircuitBreakerTick <= 1 ? this.updateSoundManager(this.ejabberdConnection.showBlocker ? AudibleSoundStates.PLAYING : AudibleSoundStates.STOPPED) : null;
                this.logger.error(`Error during connection to the XMPP server\nStatus: ${status} Condition: ${condition}`);
                break;
            case connStatus.CONNECTING:
                this.ejabberdConnection = { connected: false, showBlocker: this.connFailCircuitBreakerTick >= 2, initialLoad: false };
                this.setEjabberdConnectivity();
                this.logger.log(`Connecting to the XMPP server\nStatus: ${status} Condition: ${condition}`);
                break;
            case connStatus.CONNFAIL:
                this.ejabberdConnection = { connected: false, showBlocker: true, initialLoad: false };
                this.setEjabberdConnectivity();
                this.updateSoundManager(AudibleSoundStates.STOPPED);
                this.logger.error(`Connection to the XMPP server failed\nStatus: ${status} Condition: ${condition}`);
                break;
            case connStatus.AUTHENTICATING:
                this.ejabberdConnection = { connected: false, showBlocker: true, initialLoad: false };
                this.setEjabberdConnectivity();
                this.logger.log(`Authenticating to the XMPP server\nStatus: ${status} Condition: ${condition}`);
                break;
            case connStatus.AUTHFAIL:
                this.ejabberdConnection = { connected: false, showBlocker: true, initialLoad: false };
                this.setEjabberdConnectivity();
                this.updateSoundManager(AudibleSoundStates.STOPPED);
                this.logger.error(`Authorization to the XMPP server failed\nStatus: ${status} Condition: ${condition}`);
                break;
            case connStatus.CONNECTED:
                this.logger.log(`Successfully connected to the XMPP server\nStatus: ${status} Condition: ${condition}`);
                let initialPageState = false;
                if (!this.connFailCircuitBreakerTick) { initialPageState = true; }
                this.ejabberdConnection = { connected: true, showBlocker: false, initialLoad: initialPageState };
                this.connFailCircuitBreakerTick = 0;
                this.setEjabberdConnectivity();
                this.updateSoundManager(AudibleSoundStates.STOPPED);
                this.outgoingMessageManager.sendInitialPresence(this.connection);
                this.outgoingMessageManager.enableCarbons(this.connection, this.connectionCredentials);
                this.outgoingMessageManager.queryArchives(this.connection);
                break;
            case connStatus.DISCONNECTED:
                /* Do not show any distress signals. */
                this.ejabberdConnection = { connected: false, showBlocker: false, initialLoad: false };

                /* Increment the fail safe up by one. */
                this.connFailCircuitBreakerTick += 1;

                /* Attempt to retry the connection to the server, ensuring the connectivity is actually down. */
                this.connect(this.connectionCredentials);

                /* Enable the ejabberd banner if the connection is considered disconnected, twice. This prevents error being called, and disconnected being called after, this prevents the flicker. */
                this.ejabberdConnection.showBlocker = this.connFailCircuitBreakerTick >= 2;

                /* Set the state of the EjabberdConnection in the Redux store. */
                this.setEjabberdConnectivity();

                /* Set threshold timer, fail safe if the app loses connectivity. */
                this.ejabberdTimeout();

                /* Update the sound manager, play a sound if the ejabberd banner is showing. */
                this.connFailCircuitBreakerTick <= 1 ? this.updateSoundManager(this.ejabberdConnection.showBlocker ? AudibleSoundStates.PLAYING : AudibleSoundStates.STOPPED) : null;

                /* Log a console warning that the Ejabberd server has been disconnected. This is to handle the blips in communication from Ejabberd that falsly reports the disconnected status. */
                (this.ejabberdConnection.showBlocker === true) ? this.logger.warn(`Disconnected from the XMPP server\nStatus: ${status} Condition: ${condition}`) : null;
                break;
            case connStatus.DISCONNECTING:
                this.ejabberdConnection = { connected: false, showBlocker: true, initialLoad: false };
                this.setEjabberdConnectivity();
                this.updateSoundManager(AudibleSoundStates.STOPPED);
                this.logger.warn(`Disconnecting from the XMPP server\nStatus: ${status} Condition: ${condition}`);
                break;
            default:
                this.ejabberdConnection = { connected: false, showBlocker: true, initialLoad: false };
                this.setEjabberdConnectivity();
                this.updateSoundManager(AudibleSoundStates.STOPPED);
                this.logger.error(`Impossible strophe status detected\nStatus: ${status} Condition: ${condition}`);
        }
    }

    /**
     * Determine if the stanza passed indicates that message archive retrieval is or is not complete
     * @param {Element} stanza The stanza from which to evaluate if the archive retrieval is complete
     * @returns {boolean} - True if the message archive retrieval process is NOT finished, false otherwise
     */
    private shouldQueryArchiveAfter(stanza: Element): boolean {
        let finElement = stanza.getElementsByTagName("fin")[0];
        if (!finElement) {
            return false;
        }

        let namespace = finElement.getAttribute("xmlns");
        if (!(EjabberdConstants.archivesXMLNS.toLowerCase() === namespace.toLowerCase())) {
            return false;
        }

        let completeAttribute = finElement.getAttribute("complete");
        if (!completeAttribute || completeAttribute.toLowerCase() === "true") {
            return false;
        }

        return true;
    }

}


