import { AbstractAction } from "./_Abstract.Action";
import { Profile } from "../State/Authentication.State";

interface AuthenticationStateActionPayload {
    id_token?: string,
    access_token?: string,
    token_type?: string,
    expires_at?: number,
    scope?: string,
    state?: string,
    session_state?: string,
    profile?: Profile
}

export const authenticationStateActionTypes = {
    NEW_AUTH: "NEW_AUTH", 
    EMPTY: "EMPTY"
};

export class AuthenticationStateAction extends AbstractAction {
    public payload: AuthenticationStateActionPayload
}


