import {ConfigurationManager} from "../Configuration/ConfigurationManager";

export class LogManager {

    private configManager: ConfigurationManager;
    private console: Console;

    constructor(configurationManager: ConfigurationManager,
        console: Console) {
        this.configManager = configurationManager;
        this.console = console;
    }
    
    /**
     * Method to log informational/debug information
     */
    info(toInfo: string) {
        if (this.configManager.envConfig.messagingWebClient.enableConsoleLogging) {
            this.console.info(toInfo);
        }
    }

    /**
     * Method to log information 
     */
    log(toLog: string) {
        if (this.configManager.envConfig.messagingWebClient.enableConsoleLogging) {
            this.console.log(toLog);
        }
    }

    /**
     * Method to log warning level information
     */
    warn(toWarn: string) {
        if (this.configManager.envConfig.messagingWebClient.enableConsoleLogging) {
            this.console.warn(toWarn);
        }
    }

    /**
     * Method to log errors
     */
    error(toError: string) {
        if (this.configManager.envConfig.messagingWebClient.enableConsoleLogging) {
            this.console.error(toError);
        }
    }
}



