import { AxiosResponse } from "axios";
import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";
import { FeatureFlag } from "../../State/FeatureFlags/FeatureFlags.State";
import { FeatureFlagsStateAction, featureFlagsStateActionTypes } from "../../Action/FeatureFlags.State.Action";


interface FeatureFlagsApiResponse extends AxiosResponse {
    data: FeatureFlag
}

/**
 * Manager class to handle all pieces related to the feature flagging.
 */
export class FeatureManager {
        private storeAccess: StoreAccessManager;
        
        constructor(storeAccess: StoreAccessManager) {
            this.storeAccess = storeAccess;
        }

        /**
         * Resolve a promise from the GET /Features method and store the results in the redux store.
         * @param {FeatureFlagsApiResponse} - The axios promise to resolve.
         */
        public resolveFeatureFlags(response: FeatureFlagsApiResponse): void {
            let featureFlagsAction: FeatureFlagsStateAction = {
                type: featureFlagsStateActionTypes.UPDATE_FEATURE_FLAGS,
                payload: {
                    featureFlags: {
                        Mobile_DisplayAlerts: null,
                        Web_DisplayAlerts: null
                    }
                }
            };

            if(response){
                featureFlagsAction.payload.featureFlags.Web_DisplayAlerts = response.data.Web_DisplayAlerts;
            }

            this.storeAccess.dispatchAction(featureFlagsAction);
        }
}


