import { PersonSearchResult } from "../../../../State/PeopleSearch.State";
import * as React from "react";
import { Person } from "./Person.Presenter";

interface RecipientProps {
    recipient: PersonSearchResult,
    removeRecipient: () => void,
}

export const Recipient: React.StatelessComponent<RecipientProps> = (props: RecipientProps) => {
    let fullName = `${props.recipient.firstName} ${props.recipient.lastName}`;

    return (
        <li data-qa="recipient_name"
            className="recipient-list-item"
            title={`${props.recipient.jid.toLowerCase()}: ${fullName}`}>
            <Person
                jid={props.recipient.jid.toLowerCase()}
                firstName={props.recipient.firstName}
                lastName={props.recipient.lastName}
                removeRecipient={props.removeRecipient}
            />
        </li>
    )
};



