import * as React from "react";
import {LoadingSpinner} from "../../Components/Icons/LoadingSpinner";

export class LoginPresenter extends React.Component<null, null> {
    public render() {
        return (
            <div>
                <LoadingSpinner
                    altText="Loading..."
                    dataQa="loading-icon"
                    imageUrl="/web-messaging/lib/images/loading_spinner.gif"
                    styles="icon-loading"
                />&nbsp;<span>Loading...</span>
            </div>
        );
    }
}



