import { Reducer } from "redux";
import { MessagesUIState, MainPresenter } from "../State/Messages.UI.State";
import { messagesUIActionTypes, MessagesUIAction } from "../Action/Messages.UI.State.Action"

let initialState: MessagesUIState = { sidebarTab: 0, mainPresenter: MainPresenter.empty, recipients: [], messageText: "", activeThread: null, activeAlert: null, isConnected: null};

export const messagesUIStateReducer: Reducer<MessagesUIState> = (state: MessagesUIState = initialState, action: MessagesUIAction) => {
    let updatedState: MessagesUIState;
    switch(action.type) {
        case messagesUIActionTypes.CHANGE_MAIN_PRESENTER:
            // 0 is a falsey value so checking if(!action.payload.mainPresenter) returns false on the valid value of 0.
            if(action.payload.mainPresenter === undefined || action.payload.mainPresenter === null) { throw("No new main presenter specified"); }
            updatedState = { ...state, mainPresenter: action.payload.mainPresenter };
            break;
        // FML JMR 07/11/2017 - This case may no longer be necessary as tabs are no longer displayed. Reevaluate after next UX checkin.
        case messagesUIActionTypes.UPDATE_TAB:
            if(action.payload.tab === undefined || action.payload.tab === null) { throw("No new tab specified"); }
            updatedState = { ...state, sidebarTab: action.payload.tab };
            break;
        case messagesUIActionTypes.UPDATE_ACTIVE_THREAD:
            // NOTE - null means no thread is active
            if(action.payload.activeThread === undefined) { throw("No thread specified"); }
            updatedState = { ...state, activeThread: action.payload.activeThread };
            break;
        case messagesUIActionTypes.UPDATE_RECIPIENTS:
            if(action.payload.recipients === undefined || action.payload.recipients === null) { throw ("Recipients must not be null or undefined"); }
            updatedState = { ...state, recipients: action.payload.recipients };
            break;
        case messagesUIActionTypes.CLEAR_MESSAGE_CONTENTS: 
            updatedState = { ...state, messageText: "", recipients: []};
            break;
        case messagesUIActionTypes.UPDATE_MESSAGE_TEXT:
            if(action.payload.messageText === undefined || action.payload.messageText === null ) { throw("No message text specified"); }
            updatedState = { ...state, messageText: action.payload.messageText };
            break;
        default: 
            updatedState = state;
    }
    return updatedState;
};


