import { AbstractAction } from "./_Abstract.Action";
import { AlertState, Alert, Room, AlertStates, AlertParticipant } from "../State/Alert.State";


export const alertStateActionTypes = { 
    CREATE_ALERT: "CREATE_ALERT",
    ALERT_MESSAGE: "ALERT_MESSAGE",
    ALERT_READ: "ALERT_READ",

    UPDATE_PARTICIPANT_STATE: "UPDATE_PARTICIPANT_STATE",

    _SET_USER: "_SET_USER"
};

interface AlertStateActionPayload {
    user?: string,

    // Populate when building NEW alerts
    alert?: Alert,

    // Populate when updating an existing thread
    alertIndex?: number,
    alertMessage?: Element,
    participant?: AlertParticipant,

    // Populate when creating a new room
    room?: Room,

    // Populate when updating an existing room
    roomIndex?: number,
    roomMessage?,
}


export class AlertStateAction extends AbstractAction {
    payload: AlertStateActionPayload
}


