import { Reducer } from "redux";

import { EjabberdConnectivityStateAction, ejabberdConnectivityStateActionTypes } from "../Action/EjabberdConnectivity.State.Action";
import { EjabberdConnection, EjabberdConnectivityState } from "../State/EjabberdConnectivity.State";

let initialState: EjabberdConnectivityState = { ejabberdConnection: null };
export const ejabberdConnectivityStateReducer: Reducer<EjabberdConnectivityState> = (state: EjabberdConnectivityState = initialState, action: EjabberdConnectivityStateAction) => {
    let updatedState: EjabberdConnectivityState;
    switch(action.type) {
        case ejabberdConnectivityStateActionTypes.ESTABLISHED_CONNECTION: 
            updatedState = { ejabberdConnection: action.payload.isConnected };
            return updatedState;
        case ejabberdConnectivityStateActionTypes.INITIAL_CONNECTION:
            updatedState = { ejabberdConnection: action.payload.isConnected };
            return updatedState;
        default: 
            return state;
    }
};


