import * as React from "react";
import * as DocumentTitle from "react-document-title";
import { Thread } from "../../State/Chat.State";
import { AppTitleManager } from "./AppTitle.Manager";

interface AppTitleProps {
    appTitleManager: AppTitleManager,
    title: string,
    unreadThreads: Thread[]
}

export class AppTitle extends React.Component<AppTitleProps, {}>  {

    constructor() {
        super();
    }

    render() {
        return (
            <DocumentTitle title={this.props.title} />
        );
    }

    /**
     * Hook into the lifecycle to adjust title state when unread threads change. 
     * @param nextProps
     */
    public componentWillReceiveProps(nextProps) {
        //fast comparison if unread threads have changed at all. 
        if (JSON.stringify(this.props.unreadThreads) !== JSON.stringify(nextProps.unreadThreads)) {
            nextProps.appTitleManager.updateAppTitleRotation(nextProps.unreadThreads);
        }
    }
    
    /**
     * Update the component when the title changes. 
     * @param nextProps
     */
    public shouldComponentUpdate(nextProps: AppTitleProps): boolean {
        return nextProps.title !== this.props.title;
    }

};



