import { IHttpRequester } from "../../Injectables/Axios.Injectable";
import { AxiosResponse, AxiosRequestConfig, AxiosPromise } from "axios";
import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";
import { LogManager } from "../Log/LogManager";

import { PersonSearchResult } from "../../State/PeopleSearch.State";
import { PeopleSearchAction, peopleSearchActionTypes } from "../../Action/PeopleSearch.State.Action";

/**
 * The necessary information to configure the search SearchManager
 * Currently (03/21/2017) sourced from the config.json file in the /Config directory
 */
export interface SearchConfig {
    root: string,
    search: string,
    configEndpoint: string
}

/**
 * The shape of the data returned from the CCP messaging search API 
 */
interface SearchResults extends AxiosResponse {
    data: { pagesize: number, results: PersonSearchResult[] }
}

export class SearchManager {
    public _config: SearchConfig;
    public _storeAccess: StoreAccessManager;
    public _httpRequester: IHttpRequester;
    public _logger: LogManager;

    constructor(storeAccess: StoreAccessManager, config: SearchConfig, httpRequester: IHttpRequester, logger: LogManager) {
        this._config = config;
        this._storeAccess = storeAccess;
        this._httpRequester = httpRequester;
        this._logger = logger;
    }

    /**
     * Initiates a GET against the messaging search API and returns the promise to be resolved
     * @param accessToken The token required for access to the messaging API
     * @param query The search term
     */
    search(accessToken: string, query: string): AxiosPromise {
        if (!accessToken) {
            throw ("Invalid token.");
        }
        if (!query) {
            throw ("No person search query provided.");
        }
        let url = this._config.search;
        let queryUrl = `${url}?searchQuery=${query}`
        this._logger.info(`Dispatching a search to ${queryUrl}`);
        return this._httpRequester.get(queryUrl, { headers: { "Authorization": `Bearer ${accessToken}` } });
    }

    /**
     * Take a promise generated by the search method and resolve it dispatching the results to the redux store.
     * @param {AxiosPromise} promise - The promise generated by the search method to be resolved 
     */
    resolveSearch(promise: AxiosPromise) {
        if (!promise) {
            throw ("No person search promise provided to resolve.");
        }
        this._logger.info(`Resolving search`);
        promise.then((value: SearchResults) => this._dispatchSearchResults(value));
    }

    /**
     * Dispatch an action to update the redux store with the passed search results.
     * @param value The object containing the search results to be dispatched
     */
    _dispatchSearchResults(value: SearchResults) {
        let action: PeopleSearchAction = {
            type: peopleSearchActionTypes.SEARCH,
            payload: {
                data: !!value.data.results ? value.data.results : []
            }
        };
        this._storeAccess.dispatchAction(action);
    }
}



