import { ReducersMapObject } from "redux";
import { authenticationStateReducer } from "./Authentication.State.Action.Reducer";
import { peopleSearchStateReducer } from "./PeopleSearch.State.Action.Reducer";
import { messagesUIStateReducer } from "./Messages.UI.State.Action.Reducer";
import { configurationStateReducer } from "./Configuration.State.Action.Reducer";
import { chatStateReducer } from "./Chat.State.Action.Reducer";
import { alertStateReducer } from './Alert.State.Action.Reducer';
import { profileStateReducer } from "./Profile.State.Action.Reducer";
import { featureFlagsStateReducer } from "./FeatureFlag.State.Action.Reducer"
import { ejabberdConnectivityStateReducer } from "./EjabberdConnectivity.State.Action.Reducer";
import { audibleSoundsStateReducer } from "./AudibleSounds.State.Action.Reducer";
import { AppTitleReducer } from "../Components/AppTitle/AppTitle.Reducer";

export const reducerMap: ReducersMapObject = {
    authenticationState: authenticationStateReducer,
    peopleSearchState: peopleSearchStateReducer,
    messagesUIState: messagesUIStateReducer,
    configurationState: configurationStateReducer,
    chatState: chatStateReducer,
    connectivityState: ejabberdConnectivityStateReducer,
    alertState: alertStateReducer,
    profileState: profileStateReducer,
    featureFlagsState: featureFlagsStateReducer,
    audibleSoundsState: audibleSoundsStateReducer,
    appTitleState: AppTitleReducer
};



