import { Reducer } from "redux";

import { AuthenticationState } from "../State/Authentication.State"
import { authenticationStateActionTypes, AuthenticationStateAction } from "../Action/Authentication.State.Action";

export const initialState: AuthenticationState = { profile: null, id_token: null, access_token: null, scope: null, state: null, session_state: null, expires_at:null, token_type: null };
export const authenticationStateReducer: Reducer<AuthenticationState> = (state: AuthenticationState= initialState, action: AuthenticationStateAction) => {
    let updatedState: AuthenticationState;
    switch(action.type){
        case authenticationStateActionTypes.EMPTY:
            updatedState = initialState;
            break;
        case authenticationStateActionTypes.NEW_AUTH:
            let payload = action.payload;
            updatedState = {
                id_token: payload.id_token, 
                access_token: payload.access_token, 
                scope: payload.scope, 
                session_state: payload.session_state, 
                state: payload.state, 
                expires_at: payload.expires_at, 
                token_type: payload.token_type,
                profile: payload.profile,
            };
            break;
        default: 
            updatedState = state;
    }
    return updatedState;
};


