import { AbstractAction } from "./_Abstract.Action";
import { EjabberdConnection } from "../State/EjabberdConnectivity.State";

interface EjabberdConnectivityStateActionPayload {
    isConnected?: EjabberdConnection
}

/**
 * The ejabberd state action type for established connection and initial connection to prevent the blocker showing on initial load.
 */
export const ejabberdConnectivityStateActionTypes = {
    ESTABLISHED_CONNECTION: "ESTABLISHED_CONNECTION",
    INITIAL_CONNECTION: "INITIAL_CONNECTION"
};

/**
 * The ejabberd state action containing the payload of the ejabberdconnection.
 */
export class EjabberdConnectivityStateAction extends AbstractAction {
    payload: EjabberdConnectivityStateActionPayload
}


