/*----------  React  ----------*/
import * as React from "react";

/*----------  React Sound: https://github.com/leoasis/react-sound   ----------*/
import Sound from 'react-sound';

/*----------  Audible Sound Manager  ----------*/
import { AudibleSoundsManager } from "../../../Managers/AudibleSounds/AudibleSounds.Manager";

/*----------  Sound State  ----------*/
import { AudibleSound } from '../../../State/Notification-Sounds/AudibleSounds.State';


interface SoundProps {
    url: string,
    playStatus: string,
    audibleSoundsManager: AudibleSoundsManager
}

interface SoundState {
    playStatus: string
}

export class AudibleSounds extends React.Component<SoundProps, SoundState> {
    constructor(props) {
        super(props);
    }

    public render() {
        return (
            <Sound
                url={this.props.url}
                playStatus={this.props.playStatus}
                onFinishedPlaying={(playStatus: AudibleSound) => this.updatePlayStatus("STOPPED")}
                debugMode={false}
            />
        )
    }

    /**
     * Update the playstatus after the sound has finished playing.
     */
    private updatePlayStatus(status: string) {
        /* Get the current state for the audible sounds. */
        let audibleSound = this.props.audibleSoundsManager.getSoundState();

        /* Update the properties to disable the sound. */
        audibleSound.Sound.PlayStatus = status;
        audibleSound.Sound.HasPlayed = true;

        /* Call to the service to update the state in the redux store. */
        this.props.audibleSoundsManager.setSoundState(audibleSound.Sound);
    }
}


