import { Reducer } from "redux";

import { ChatState, Thread, Participant } from "../State/Chat.State";
import { chatStateActionTypes, ChatStateAction } from "../Action/Chat.State.Action";

export const initialState: ChatState = {threads: [], rooms: [], user: ""};
export const chatStateReducer: Reducer<ChatState> = (state: ChatState = initialState, action: ChatStateAction) => {
    let updatedState: ChatState;
    switch(action.type) {
        case chatStateActionTypes._SET_USER:
            if(action.payload.user === null || action.payload.user === undefined) {
                throw("No user provided to set chat state");
            }
            updatedState = {
                ...state, 
                user: action.payload.user
            };
            break;
        case chatStateActionTypes.CREATE_THREAD:
            if(action.payload.thread === null || action.payload.thread === undefined) {
                throw("No thread provided to create");
            }
            let updatedThreads = state.threads;
            updatedThreads = updatedThreads.concat(action.payload.thread);
            updatedState = { 
                ...state,
                threads: updatedThreads
            };
            break;
        case chatStateActionTypes.THREAD_MESSAGE:
            if(action.payload.threadIndex === null || action.payload.threadIndex === undefined) { 
                throw("No thread index specified for thread messages update"); 
            }
            if(action.payload.threadMessage === null || action.payload.threadMessage === undefined) {
                throw("No new thread messages provided");
            }
            const updatedMessages = state.threads[action.payload.threadIndex].messages.concat(action.payload.threadMessage);
            let wasFromCurretnUser = (action.payload.threadMessage.getAttribute("from").split("/")[0].toLowerCase() === state.user);
            const updatedThread: Thread = { 
                ...state.threads[action.payload.threadIndex], 
                messages: updatedMessages, 
                unreadCount: (wasFromCurretnUser) ? 0 : state.threads[action.payload.threadIndex].unreadCount + 1
            };
            let threads = state.threads.slice(0, action.payload.threadIndex);
            threads = threads.concat(updatedThread); 
            threads = threads.concat(state.threads.slice(action.payload.threadIndex+1));
            updatedState = {
                ...state,
                threads: threads
            };
            break;
        case chatStateActionTypes.THREAD_READ:
            if(action.payload.threadIndex === null || action.payload.threadIndex === undefined) {
                throw("Thread index not provided to specify what thread was read");
            }
            const readThread: Thread = { 
                ...state.threads[action.payload.threadIndex],
                unreadCount: 0
            };
            const markedThreads = sliceThreads(state.threads, readThread, action.payload.threadIndex);
            updatedState = {
                ...state,
                threads: markedThreads
            };
            break;
        case chatStateActionTypes.UPDATE_PARTICIPANT_STATE:
            if(action.payload.threadIndex === null || action.payload.threadIndex === undefined){
                throw("No thread provided for the chat state update");
            }
            else if(action.payload.participant === null || action.payload.participant === undefined){
                throw("No participant provided to update chat state for");
            }
            let participantIndex = -1;
            state.threads[action.payload.threadIndex].participants.forEach((participant, index) => {
                if(participant.jid.toLowerCase() === action.payload.participant.jid.toLowerCase()){
                    participantIndex = index; 
                }
            });
            if(participantIndex === -1) { 
                throw(`Participant ${action.payload.participant.jid.toLowerCase()} not found in thread: ${state.threads[action.payload.threadIndex].id}`)
            }
            let updatedParticipants = sliceParticipants(state.threads[action.payload.threadIndex].participants, action.payload.participant, participantIndex);
            let threadWithUpdatedParticipants: Thread = { 
                ...state.threads[action.payload.threadIndex],
                participants: updatedParticipants
            };
            let threadsWithUpdatedParticipants = sliceThreads(state.threads, threadWithUpdatedParticipants, action.payload.threadIndex);
            updatedState = { 
                ...state,
                threads: threadsWithUpdatedParticipants
            };
            break;
        default: 
            updatedState = state;
    }
    return updatedState;
};

/**
 * Slice the passed threads array inserting the passed thread at index, leaving the original array unaltered.
 * @param {Thread[]} threads - The threads to be sliced
 * @param {Thread} updatedThread - The thread to be inserted at index 
 * @param {number} index - The index at which to slice
 */
function sliceThreads(threads: Thread[], updatedThread: Thread, index: number): Thread[] {
    let updatedThreads = threads.slice(0, index).concat(updatedThread).concat(threads.slice(index+1));
    return updatedThreads
}

function sliceParticipants(participants: Participant[], updatedParticipant: Participant, index: number): Participant[] {
    let updatedParticipants = participants.slice(0, index).concat(updatedParticipant).concat(participants.slice(index+1));
    return updatedParticipants
}


