import * as React from "react";
import { Icon } from "../../../../Components/Icons/Icon";

interface PersonProps {
    jid: string;
    firstName: string;
    lastName: string;
    removeRecipient: () => void;
}

export const Person: React.StatelessComponent<PersonProps> = (props: PersonProps) => {
    const fullName = `${props.firstName} ${props.lastName}`;
    const removeTitle = `Remove ${props.firstName}`;
    const condensedName = fullName.length > 15 ? `${fullName.substring(0, 14)}...` : fullName;
    return (
        <div className="person-block">
            <a className="person-name text-center" data-qa="tag_remove_img" title={removeTitle}>
                {condensedName}
                <Icon
                    data-qa="tag_remove_img"
                    identifier="close"
                    onClick={() => {
                        props.removeRecipient();
                    }}
                    styles="icon-person-remove"
                />
            </a>
        </div>
    );
};



