import * as React from "react";
import {Icon} from "../../../../Components/Icons/Icon";
import {MainPresenter} from "../../../../State/Messages.UI.State";

interface EmptyMainProps {
    changeMainPresenter: (main: MainPresenter) => void,
}

export const EmptyMain: React.StatelessComponent<EmptyMainProps> = (props: EmptyMainProps) => {
    return (
        <div id="main" className="emptyWrapper">
            <h1>
                <Icon
                    identifier="inbox"
                    styles="icon-inbox-splash"
                />
            </h1>
            <h2>Welcome to Spok</h2>
            <h3>
                <Icon
                    dataQa="new_message_lbl"
                    identifier="compose"
                    onClick={() => {
                        props.changeMainPresenter(MainPresenter.compose);
                    }}
                    styles="icon-compose-splash"
                />
                <span>Compose Message</span>
            </h3>
        </div>
    );
};



