import * as React from "react";

interface LoadingSpinnerProps {
    altText?: string;
    dataQa?: string;
    imageUrl?: string;
    styles?: string;
}

export const LoadingSpinner: React.StatelessComponent<LoadingSpinnerProps> = (props: LoadingSpinnerProps) => {
    return (
        <img src={`${props.imageUrl}`} data-qa={props.dataQa ? props.dataQa : null}
             className={props.styles ? props.styles : null} alt={props.altText ? props.altText : null}/>
    );
};



