import { AbstractAction } from "./_Abstract.Action";
import { MessagesUIState, SidebarTab, MainPresenter } from "../State/Messages.UI.State";
import { Thread } from "../State/Chat.State";
import { Alert } from '../State/Alert.State';

// This feels dangerously coupled... abstract that interface out of the people search state file into its own generic interface directory probably. Also rename it.
import { PersonSearchResult } from "../State/PeopleSearch.State";

export const messagesUIActionTypes = { 
    UPDATE_TAB: "UPDATE_TAB", // FML JMR 07/11/2017 - This case may no longer be necessary as tabs are no longer displayed. Reevaluate after next UX checkin.
    UPDATE_ACTIVE_THREAD: "UPDATE_ACTIVE_THREAD",
    CHANGE_MAIN_PRESENTER: "CHANGE_MAIN_PRESENTER",
    UPDATE_RECIPIENTS: "UPDATE_RECIPIENTS",
    CLEAR_MESSAGE_CONTENTS: "CLEAR_MESSAGE_CONTENTS",
    UPDATE_MESSAGE_TEXT: "UPDATE_MESSAGE_TEXT"
};

interface MessagesUIActionPayload {
    tab?: SidebarTab, // FML JMR 07/11/2017 - This case may no longer be necessary as tabs are no longer displayed. Reevaluate after next UX checkin.
    mainPresenter?: MainPresenter,
    recipients?: PersonSearchResult[],
    messageText?: string,
    activeThread?: Thread,
    activeAlert?: Alert
}

export class MessagesUIAction extends AbstractAction {
    payload: MessagesUIActionPayload
}


