import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";

import { Alert } from "../../state/Alert.State";
import { Thread } from "../../State/Chat.State";
import { PersonSearchResult } from "../../State/PeopleSearch.State";
import { MainPresenter, SidebarTab } from "../../State/Messages.UI.State";
import { MessagesUIAction, messagesUIActionTypes } from "../../Action/Messages.UI.State.Action";
import * as _ from 'lodash';

export class MessagesUiManager {
    public storeAccess: StoreAccessManager;

    constructor(storeAccess: StoreAccessManager) {
        this.storeAccess = storeAccess;
    }

    /**
     * Set the active sidebar tab
     * @param {SidebarTab} sidebarTab - The sidebar tab to be marked as active
     */
    public updateSideBarTab(sidebarTab: SidebarTab) {
        let action: MessagesUIAction = { type: messagesUIActionTypes.UPDATE_TAB, payload: { tab: sidebarTab } };
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Set the main presenter
     * @param {MainPresenter} mainPresenter - The main presenter to be displayed
     */
    public updateMainPresenter(mainPresenter: MainPresenter) {
        let action: MessagesUIAction = {
            type: messagesUIActionTypes.CHANGE_MAIN_PRESENTER,
            payload: { mainPresenter: mainPresenter }
        };
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Update the list of potential recipients
     * @param {PersonSearchResult[]} recipients - The list of potential recipients from the messaging API
     */
    public updateRecipients(recipients: PersonSearchResult[]) {
        let action: MessagesUIAction = {
            type: messagesUIActionTypes.UPDATE_RECIPIENTS,
            payload: { recipients: recipients }
        };
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Update the message text in the redux store.
     * @param {string} messageText - The new contents of the users message
     */
    public updateMessageText(messageText: string) {
        let action: MessagesUIAction = {
            type: messagesUIActionTypes.UPDATE_MESSAGE_TEXT,
            payload: { messageText: messageText }
        };
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Update the active thread to the passed thread
     * @param {Thread} activeThread - The thread to be moved into the active slot
     */
    public setActiveThread(activeThread: Thread) {
        let action: MessagesUIAction = {
            type: messagesUIActionTypes.UPDATE_ACTIVE_THREAD,
            payload: { activeThread: activeThread }
        };
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Updat the active thread to the passed thread
     * @param {Alert} activeAlert - The thread to be moved into the active slot
     */
    public setActiveAlert(activeAlert: Alert) {
        let action: MessagesUIAction = {
            type: messagesUIActionTypes.UPDATE_ACTIVE_THREAD,
            payload: { activeAlert: activeAlert }
        };
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Empty out the message text in the redux store.
     */
    public clearMessageText() {
        let action: MessagesUIAction = { type: messagesUIActionTypes.CLEAR_MESSAGE_CONTENTS, payload: {} };
        this.storeAccess.dispatchAction(action);
    }

    /**
     * Prepare the thread title given its participants in alphabetical order
     */
    public buildThreadTitle(thread: Thread, userJid: string): string {
        let possibleThreadNames = _.sortBy(thread.participants, p => p.name.toLocaleLowerCase());
        if (possibleThreadNames[0].jid.toLowerCase() === userJid.toLowerCase() && possibleThreadNames.length > 1) {
            possibleThreadNames = possibleThreadNames.splice(1, possibleThreadNames.length);
        }
        let threadName: string = possibleThreadNames[0].name;

        switch (thread.participants.length) {
            case 1:
                return "#1";
            case 2:
                return `${threadName}`;
            case 3:
                return `${threadName} and 1 other`;
            default:
                return `${threadName} and ${thread.participants.length - 2} others`;
        }

    }
}



