import * as React from "react";
import { Participant } from "../../../../../State/Chat.State";

interface ThreadRecipientsProps {
    participants: Participant[]
    userJid: string
}

export const ThreadRecipients: React.StatelessComponent<ThreadRecipientsProps> = (props: ThreadRecipientsProps) => {
    let recipientList: JSX.Element[] = [];
    let lowerCasedUserJid = props.userJid.toLowerCase();

    props.participants.forEach((participant) => {
        let participantJid = participant.jid.toLowerCase();
        if (participantJid !== lowerCasedUserJid) {
            recipientList.push(<div key={participantJid}>{participant.name}</div>)
        }
    });

    return (
        <div className="thread-recipients" data-qa="thread-participant-container">
            {recipientList}
        </div>
    );
};



