import { AbstractAction } from "./_Abstract.Action";
import { ChatState, Thread, Room, participantChatStates, Participant } from "../State/Chat.State";


export const chatStateActionTypes = { 
    CREATE_THREAD: "CREATE_THREAD",
    THREAD_MESSAGE: "THREAD_MESSAGE",
    THREAD_READ: "THREAD_READ",

    UPDATE_PARTICIPANT_STATE: "UPDATE_PARTICIPANT_STATE",

    _SET_USER: "_SET_USER"
};

interface ChatStateActionPayload {
    user?: string,

    // Populate when building NEW threads
    thread?: Thread,

    // Populate when updating an existing thread
    threadIndex?: number,
    threadMessage?: Element,
    participant?: Participant,

    // Populate when creating a new room
    room?: Room,

    // Populate when updating an existing room
    roomIndex?: number,
    roomMessage?,
}


export class ChatStateAction extends AbstractAction {
    payload: ChatStateActionPayload
}


