import * as React from "react";
import { Icon } from "../../../../Components/Icons/Icon";
import { Participant } from "../../../../State/Chat.State";
import AutoResizeTextArea from "react-textarea-autosize";

interface SendMessageProps {
    placeHolderText: string;
    recipients: Participant[];
    sendMessage: (to: Participant[], message: string) => void;
    minRows: number;
    maxRows: number;
}

interface SendMessageState {
    messageText: string;
}

const initialState = {
    messageText: "",
};

export class SendMessage extends React.Component<SendMessageProps, SendMessageState> {

    constructor(props) {
        super(props);
        this.state = initialState;
    }

    render() {
        const sendValidated = (this.state.messageText && this.state.messageText.trim().length > 0)
            && this.props.recipients.length > 0;
        return (
            <div id="messageTextInput">
                <div id="inputWrapper">
                    <AutoResizeTextArea
                        minRows={this.props.minRows}
                        maxRows={this.props.maxRows}
                        data-qa={"message_box_txt"}
                        value={this.state.messageText}
                        onChange={(e) => {
                            this.setState({ messageText: e.target.value });
                        }}
                        onKeyPress={(e: any) => {
                            if (e.key === "Enter") {
                                if (sendValidated) {
                                    this.sendMessageToRecipients(this.props);
                                }
                                e.preventDefault();
                            }
                        }}
                        className={"form-control input-text message-input"}
                        placeholder={this.props.placeHolderText}
                        maxLength={1600} />

                    {
                        <button data-qa="thread-message-send"
                                className={sendValidated ? "enabled" : "disabled"}
                                onClick={
                                    () => {
                                        if (sendValidated) {
                                            this.sendMessageToRecipients(this.props);
                                        }
                                    }
                                }>
                            <Icon
                                dataQa="thread-message-send"
                                identifier="send"
                                styles="icon-send-message"
                            />
                        </button>
                    }
                </div>
            </div>
        );
    }

    /**
     * Send message to the recipients on the thread
     */
    private sendMessageToRecipients(props) {
        props.sendMessage(props.recipients, this.state.messageText);
        this.setState(initialState);
    }
}



