export interface AudibleSoundState {
    Sound: AudibleSound
}

export interface AudibleSound {
    CurrentSound: string,
    Position: number,
    Volume: number,
    PlayStatus: string,
    HasPlayed: boolean
}

export const AudibleSoundStates = {
    PLAYING: "PLAYING",
    STOPPED: "STOPPED",
    PAUSED: "PAUSED"
}


