import { AppTitleState } from "./AppTitle.State";
import { AppTitleAction, AppTitleActionTypes } from "./AppTitle.Action";
import { StoreAccessManager } from "../../Managers/StoreAccess/StoreAccessManager";
import { MessagesUiManager } from "../../Managers/UI/MessagesUI.Manager";

export class AppTitleManager {
    private storeAccess: StoreAccessManager;
    private uiManager: MessagesUiManager;
    private defaultTitle: string;
    private rotationTimer: number;

    /*
    * @param {StoreAccessManager} storeAccessManager - The manager's redux store to return data from the data store.
    */
    constructor(storeAccess: StoreAccessManager, uiManager: MessagesUiManager) {
        this.storeAccess = storeAccess;
        this.uiManager = uiManager;

        this.defaultTitle = "";
    }

    /**
     * Set the default title during the app bootstrap. 
     */
    public setDefaultTitle() {

        this.defaultTitle = "Messages - " + this.storeAccess.getCurrentUser().name;

        let appTitleAction: AppTitleAction = {
            type: AppTitleActionTypes.UPDATE_TITLE,
            payload: {
                title: this.defaultTitle
            }
        };

        this.storeAccess.dispatchAction(appTitleAction);
    }

    /**
     * Add/Update app title in the redux store.
     */
    public setAppTitle(appTitle: AppTitleState) {
        let appTitleAction: AppTitleAction = {
            type: AppTitleActionTypes.UPDATE_TITLE,
            payload: {
                title: appTitle.title
            }
        };
        this.storeAccess.dispatchAction(appTitleAction);
    }

    /**
     * Start the app title rotation when there are unread threads.
     * @param unreadThreads is an array of Threads that contain unread messages
     */
    //FML change type to Thread when can find a way to create a new Element in the tests.  
    public updateAppTitleRotation(unreadThreads: any) {

        if (!this.defaultTitle) {
            throw("Default title not set. setDefaultTitle must be called.");
        }
        
        //Stop the rotation because things changed
        clearInterval(this.rotationTimer);

        let currentTitle = this.storeAccess.getAppTitleState().title;

        //Don't change the title if there is only one and it's already set. 
        if (!unreadThreads.length && this.defaultTitle === currentTitle) {
            return;
        }

        //Build the possible titles. 
        let possibleAppTitles: string[] = [];
        let userJid = this.storeAccess.getCurrentUser().jid;
        for (let i = 0; i < unreadThreads.length; i++) {       

            if (!!unreadThreads[i].messages.length) {
                //get the name of the thread
                let verb = unreadThreads[i].participants.length > 3 ? "say" : "says";
                possibleAppTitles.push(`${this.uiManager.buildThreadTitle(unreadThreads[i], userJid)} ${verb}`);
            }
        }
        possibleAppTitles.push(this.defaultTitle);
        this.setAppTitle({ title: possibleAppTitles[0] });

        //Start the rotation if there is more than one. 
        if (possibleAppTitles.length > 1) {
            let index = 0;
            this.rotationTimer = setInterval(() => {
                //reset index to 0 if it hits the end
                index = (index + 1) % possibleAppTitles.length;
                this.setAppTitle({ title: possibleAppTitles[index] });
            }, 1000);
        }
    }
}



