/*----------  Redux store manager  ----------*/
import { StoreAccessManager } from "../StoreAccess/StoreAccessManager";

/*----------  Sound State  ----------*/
import { AudibleSound } from "../../State/Notification-Sounds/AudibleSounds.State";

/*----------  Sound Action  ----------*/
import { audibleSoundsStateActionTypes, AudibleSoundsStateAction } from "../../Action/AudibleSounds.State.Action";


export class AudibleSoundsManager {
    public storeAccess: StoreAccessManager;

    /*
    * @param {StoreAccessManager} storeAccessManager - The manager's redux store to return data from the data store.
    */
    constructor(storeAccess: StoreAccessManager)
    {
        this.storeAccess = storeAccess;
    }

    /**
     * Add/Update audible sound in the redux store.
     */
    public setSoundState(audibleSound: AudibleSound) {
        let audibleSoundAction: AudibleSoundsStateAction = {
            type: audibleSoundsStateActionTypes.SET_SOUND_PRESENCE, 
            payload: {
                audibleSounds: {
                    CurrentSound: audibleSound.CurrentSound,
                    PlayStatus: audibleSound.PlayStatus,
                    Position: audibleSound.Position,
                    Volume: audibleSound.Volume,
                    HasPlayed: audibleSound.HasPlayed
                }
            }
        };
        
        this.storeAccess.dispatchAction(audibleSoundAction);
    }

    /**
     * Get the current sound from the redux store.
     */
    public getSoundState() {
        return this.storeAccess.getAudibleSounds();
    }
}



