import * as React from "react";
import * as Awesomplete from "awesomplete";
import { Icon } from "../../../../Components/Icons/Icon";
import { PersonSearchResult } from "../../../../State/PeopleSearch.State";
import { Recipient } from "./Recipient.Presenter";

// TODO This is a container refactor it AFAP, like NAOW, DO IT NAOW
interface UserSearchProps {
    recipients: PersonSearchResult[];
    updateRecipients: (recipients: PersonSearchResult[]) => void;
    people: PersonSearchResult[];
    search: (searchTerm: string) => void;
}

interface UserSearchState {
    searchTerm: string
}

export class UserSearch extends React.Component<UserSearchProps, UserSearchState> {
    private inputId: string = "UserSearchInput";
    private typeahead: Awesomplete;

    constructor(props) {
        super(props);
        this.state = { searchTerm: "" };
    }

    componentWillUnmount() {
        if (this.props.recipients.length > 0) {
            this.props.updateRecipients(new Array<PersonSearchResult>());
        }
    }

    focusOnUserSearchInputField = input => {
        if (input) {
            setTimeout(() => {
                input.focus()
            }, 100);
        }
    };

    public render() {
        let recipientList: JSX.Element[] = this.buildRecipientMarkup();
        let noResults = (this.props.people.length === 0 && this.state.searchTerm.length >= 2);

        return (
            <div className="user-search-recipient-container">
                <div className="recipient-list-container">
                    <ul className="recipient-list"> {recipientList} </ul>
                </div>
                <div className="search-container">
                    <div id="composeSearch">
                        <input
                            tabIndex={1}
                            id={this.inputId}
                            data-qa="user_search_txt"
                            onChange={e => {
                                this.setState({ searchTerm: e.target.value });
                                if (e.target.value.length >= 2) {
                                    this.props.search(e.target.value);
                                }
                            }}
                            value={this.state.searchTerm}
                            type="text"
                            data-maxitems="500"
                            ref={this.focusOnUserSearchInputField}
                            placeholder="search by name" />

                        <Icon
                            dataQa="compose_message_search"
                            identifier="search"
                            styles="icon-compose-search"
                        />

                        {noResults ?
                            <span data-qa="no-results"><br />Your search returned no results</span> : <span></span>}

                    </div>
                </div>
            </div>
        );
    }

    // TODO The fact that this latches on to an id indicates it probably SHOULDN'T be a sub component...
    public componentDidMount() {
        // Awesomplete documentation: http://leaverou.github.io/awesomplete/
        this.typeahead = new Awesomplete(document.getElementById(this.inputId),
            {
                list: [{ label: "", value: "" }], // Start with an empty list
                replace: function (text) {
                }, // Just empty the input after selection after selection.
                autoFirst: true, // ensure the first item in the list is selected automatically
            });
        document.getElementById(this.inputId).addEventListener("awesomplete-select", (event) => this.processAwesompleteSelect(event));
    }

    public componentDidUpdate() {
        this.typeahead.list = this.buildAutocompleteList();
    }

    private buildRecipientMarkup() {
        let rows: JSX.Element[] = [];
        this.props.recipients.forEach((value: PersonSearchResult) => {
            rows.push(
                <Recipient key={value.jid.toLowerCase()} recipient={value}
                    removeRecipient={() => this.removeRecipient(value.jid.toLowerCase())} />
            )
        });
        return rows;
    }

    /**
     * Awesomplete helper method to build the list that is displayed to the user.
     */
    private buildAutocompleteList(): { label: string, value: string }[] {
        let rows: { label: string, value: string }[] = [];
        if (!this.props.people) {
            return [];
        }
        this.props.people.forEach((person) => {
            rows.push({ label: `${person.firstName} ${person.lastName}`, value: person.jid.toLowerCase() });
        });
        return rows;
    }

    /**
     * Processes the users selection and adds it to recipient list if the person is both a valid user and not already in the recipient list.
     * @param {Object} event The event emitted by awesomplete. event.text.value is the contents of the input to which the awesomplete was attached.
     */
    private processAwesompleteSelect(event) {
        // FML this logic should be moved to a service class and unit tested
        let filtered = this.filterPeople(this.props.people, event.text.value);
        let existingRecipient = filtered.length > 0 ? this.props.recipients.find(recipient => {
            return recipient.userId === filtered[0].userId;
        }) : undefined;

        if (!existingRecipient) {
            let updatedRecipients = this.props.recipients.concat(filtered);
            this.props.updateRecipients(updatedRecipients);
        }

        this.setState({ searchTerm: "" });
    }

    private removeRecipient(jidToRemove: string) {
        let filtered = this.filterOutJid(this.props.recipients, jidToRemove);
        this.props.updateRecipients(filtered);
    }

    /**
     * Filters the list passed and returns an array of PersonSearchResults that match the passed jid.
     * @param {PersonSearchResult[]} listToFilter The list to be filtered.
     * @param {string} jidToFind The jid to filter for.
     * @return An array of PersonSearchResult that contains all items that match the passed jid.
     */
    private filterPeople(listToFilter: PersonSearchResult[], jidToFind: string) {
        let filtered = listToFilter.filter(function (value: PersonSearchResult) {
            if (value.jid.toLowerCase() === jidToFind.toLowerCase()) {
                return value;
            }
        });
        return filtered;
    }

    /**
     *
     * @param listToFilter
     * @param jidToEliminate
     * @return a list of the recipients with the requested jid removed.
     */
    private filterOutJid(listToFilter: PersonSearchResult[], jidToEliminate: string) {
        let filtered = listToFilter.filter(function (value: PersonSearchResult) {
            if (value.jid.toLowerCase() !== jidToEliminate) {
                return value;
            }
        });
        return filtered;
    }
}



