import { Reducer } from "redux";

import { FeatureFlagsStateAction, featureFlagsStateActionTypes } from "../Action/FeatureFlags.State.Action";
import { FeatureFlagsState } from "../State/FeatureFlags/FeatureFlags.State";

let initialState: FeatureFlagsState = { featureFlags: null };
export const featureFlagsStateReducer: Reducer<FeatureFlagsState> = (state: FeatureFlagsState = initialState, action: FeatureFlagsStateAction) => {
    let updatedState: FeatureFlagsState;
    switch(action.type) {
        case featureFlagsStateActionTypes.UPDATE_FEATURE_FLAGS:
            if(action.payload.featureFlags === null || action.payload.featureFlags === undefined) { throw("Feature data cannot be null or undefined."); }
            updatedState = {featureFlags: action.payload.featureFlags};
            return updatedState;
        default: 
            return state;
    }
};


