import * as React from "react";
import { Thread } from "../../../../../State/Chat.State";
import { ThreadMessage } from "./Thread.Message.Presenter";
import { services } from "../../../../../Managers/ServiceManager";
import { StanzaHelper } from "../../../../../Managers/XMPP/StanzaHelper";

interface ThreadMessagesProps {
    thread: Thread,
    userJid: string,
    threadMessagesRef: any,
}

export class ThreadMessages extends React.Component<ThreadMessagesProps, {}> {

    threadMessages: Element[];

    constructor(props) {
        super(props);
        this.threadMessages = []
    }

    render() {

        const messages = this.props.thread.messages.map((message, i) => {

            let timestamp = StanzaHelper.getFormattedTimestamp(message);
            let fromName = "";

            let fromTag = message.getElementsByTagName("message")[0] ?
                message.getElementsByTagName("message")[0].getAttribute("from").split("/")[0] :
                message.getAttribute("from").split("/")[0];

            this.props.thread.participants.forEach((participant) => {
                if (participant.jid.toLowerCase() === fromTag.toLowerCase()) {
                    fromName = participant.name;
                }
            });


            let isMyMessage: boolean = fromTag.toLowerCase() === this.props.userJid.toLowerCase();
            let messageTextContent = StanzaHelper.getBodyContent(message);
            let messageId = message.getElementsByTagName("message")[0] ?
                message.getElementsByTagName("message")[0].getAttribute("id") :
                message.id;
            let key = `${i}_${messageId}`;

            return <ThreadMessage
                timestamp={timestamp}
                fromName={fromName}
                fromTag={fromTag}
                isMyMessage={isMyMessage}
                key={key}
                messageTextContent={messageTextContent}
                threadMessageRef={element => this.threadMessages[i] = element}
            />

        });

        this.props.threadMessagesRef(this.threadMessages);

        return (
            <div id="messages">
                {messages}
            </div>
        );


    }
};



