import { StoreAccessManager } from "../../StoreAccess/StoreAccessManager";
import { EjabberdConnection } from "../../../State/EjabberdConnectivity.State";
import { EjabberdConnectivityStateAction, ejabberdConnectivityStateActionTypes } from '../../../Action/EjabberdConnectivity.State.Action';


export class EjabberdUiManager {
    public storeAccess: StoreAccessManager;

    /**
     * Ejabberd UI Manager Constructor
     * @param {StoreAccessManager} storeAccess - The active store.
     */
    constructor(storeAccess: StoreAccessManager) {
        this.storeAccess = storeAccess;
    }

    /**
     * Set the active Ejabberd Connection
     * @param {EjabberdConnection} ejabberdConnection - The ejabberd connection to be set.
     */
    public setEjabberdState(ejabberdConnection: EjabberdConnection) {
        if (!ejabberdConnection) {
            throw ("No connection state provided to set");
        }
        let action: EjabberdConnectivityStateAction = {
            type: ejabberdConnectivityStateActionTypes.ESTABLISHED_CONNECTION,
            payload: {
                isConnected: {
                    connected: ejabberdConnection.connected,
                    showBlocker: ejabberdConnection.showBlocker,
                    initialLoad: ejabberdConnection.initialLoad
                }
            }
        };
        this.storeAccess.dispatchAction(action);
    }
}


