import * as moment from "moment";

interface PersonName {
    firstName: string,
    lastName: string,
    middleName?: string
}

/**
 * Service class to provide basic string manipulation operations that don't belong anywhere else
 * but need to be accessible from multiple parts of the application.
 */
export class StringManipulationManager {
    constructor() {

    }

    /**
     * Take an input string containing a name and split it into the component parts, fname, lname and optionally mname.
     * @param {string} input - The string input to resolve to a name
     * @param {string} delimiter - The string pattern that separates the pieces of the name. Defaults to " ".
     * @returns {PersonName} - The object containing the person's names
     */
    public resolveNameFromString(input: string, delimiter: string = " "): PersonName {
        if (!input) { throw ("No string provided to resolve name from"); }

        let name: PersonName;
        let names = input.split(delimiter);
        switch (names.length) {
            case 1:
                throw (`${input} delimited by '${delimiter}' doesn't split into enough parts to form a name`);
            case 2:
                name = { firstName: names[0], lastName: names[1] };
                break;
            case 3:
                name = { firstName: names[0], lastName: names[2], middleName: names[1] };
                break;
            default:
                name = { firstName: names[0], lastName: names[names.length - 1] };
        }
        return name;
    }

    /**
     * Resolve a person's initials from the passed name.
     * @param {PersonName} name - The name from which to resolve initials 
     * @returns {string} - The person's first and last initial
     */
    public getInitials(name: PersonName): string {
        if (!name) { throw ("No name provided to get initials from"); }
        if (!name.firstName || !name.lastName) { throw (`${JSON.stringify(name)} is missing one or both of the names to resolve initials from`); }
        return `${name.firstName.charAt(0).toUpperCase()}${name.lastName.charAt(0).toUpperCase()}`
    }

    /**
     * Format the given timestamp to the specified format. Defaults to format "M/D/YYYY h:mm A" if one is not provided.
     * @param timestamp  - The timestamp to format
     * @param format - The format
     */
    static formatTimestamp(timestamp: string, format?: string): string {
        format = format ? format : "M/D/YYYY h:mm A";

        let formattedTimestamp = "";
        if (timestamp) {
            let messageMoment = moment(timestamp);
            if (messageMoment.isValid()) {
                formattedTimestamp = messageMoment.format(format);
            }
        }
        return formattedTimestamp;
    }
}


