import * as React from "react";

import { MainPresenter } from "../../../../../State/Messages.UI.State";
import { Alert } from "../../../../../State/Alert.State";

interface AlertPresenterProps {
    user: {name: string, jid: string},
    close: (presenter: MainPresenter) => void,
    alert: Alert
}

interface AlertPresenterState { 
    messageText: string
}

export class AlertPresenter extends React.Component<AlertPresenterProps, AlertPresenterState> {
    constructor(props){
        super(props);
        // TODO: Circle back around on this message text.
        this.state = { messageText: "" };
    }

    /**
     * Takes in the raw xml and translates it into markup for display
     * @param {Element} element - The element to translate into markup
     * @returns {JSX.Element} - The formated markup to display.
     */
    private prepareMessageForDisplay(element: Element): JSX.Element {
        const alertMessages = {
            innerMessage: 'Please visit room B503',
            headerMessage: 'Heart Failure',
            from: 'Claire'
        };

        let messageTextContent = alertMessages.innerMessage; //element.getElementsByTagName("body")[0].textContent;
        let messageId = 'fead342fh43';//element.getAttribute("id").valueOf();
        let from = alertMessages.from.toLowerCase();//element.getAttribute("from").valueOf().split("/")[0];

        //TODO unperformant hack. Fix this later. <--LOL
        let fromName = "";
        this.props.alert.participants.forEach((participant) => {
            if(participant.jid.toLowerCase() === from){ fromName = participant.name; }
        });

        return (
            <div key={messageId} data-qa={`${from}_message_lbl`} className={'alert-sidebar-critical'}>
                <p data-qa={`${from}_name_lbl`} className={"thread-message-author"}>{ fromName }</p>
                <p data-qa={`${from}_message_contents_lbl`}>{messageTextContent}</p>
            </div>
        )
    }

    render() {
        let messages: JSX.Element[] = [];
        this.props.alert.messages.forEach((message) => {
            messages.push(this.prepareMessageForDisplay(message));
        });

        return (
        <div id="thread">
            <div className="row thread-info-container">
                <div className="col-md-11">
                </div>
                <div className="col-md-1">
                    <span onClick={e => {this.props.close(MainPresenter.empty)}} className="fa fa-2x pointer fa-window-close-o pull-right"></span>
                </div>
            </div>
            <div className="row thread-messages-container">
                {messages}
            </div>
            <div className="row thread-actions-container">
                <div className="row">
                    <input 
                        data-qa={"message_box_txt"}
                        value={this.state.messageText}
                        onChange={e =>{let updatedState: AlertPresenterState = {messageText: e.target.value}; this.setState(updatedState);}} 
                        onKeyPress={e => {
                                //TODO: Make this a callback notifying of state.
                            }
                        } 
                        className={"form-control fill-height"} placeholder={"Send a message..."} />
                </div>
            </div>
        </div>
        )
    }
    
    private clearState(){
        let updatedState: AlertPresenterState = { messageText: ""};
        this.setState(updatedState);
    }
}


