import { Reducer } from "redux";

import { ProfileStateAction, profileStateActionTypes } from "../Action/Profile.State.Action";
import { ProfileState, Affiliation } from "../State/Profile.State";

let initialState: ProfileState = { userId: null, jabberId: null, isRoot: null, affiliations: [] };
export const profileStateReducer: Reducer<ProfileState> = (state: ProfileState = initialState, action: ProfileStateAction) => {
    let updatedState: ProfileState;
    switch(action.type) { 
        case profileStateActionTypes.SET_PROFILE: 
            if(action.payload.profileData === null || action.payload.profileData === undefined) { throw("Profile data cannot be null or undefined"); }
            updatedState = action.payload.profileData;
            break;
        case profileStateActionTypes.CLEAR: 
            updatedState = initialState;
            break;
        default:
            updatedState = state;
    }
    return updatedState;
};


