import { Reducer } from "redux";

import { ConfigurationStateAction, configurationStateActionTypes } from "../Action/Configuration.State.Action";
import { ConfigurationItem, ConfigurationState } from "../State/Configuration.State";

export const initialState: ConfigurationState = { xmppPort: null, xmppUri: null, identityServerUri: null };
export const configurationStateReducer: Reducer<ConfigurationState> = (state: ConfigurationState = initialState, action: ConfigurationStateAction) => {
    let updatedState: ConfigurationState;
    switch(action.type) {
        case configurationStateActionTypes.UPDATE_ALL: 
            updatedState = { xmppPort: action.payload.xmppPort, xmppUri: action.payload.xmppUri, identityServerUri: action.payload.identityServerUri };
            return updatedState;
        default: 
            return state;
    }
};


